// Google Drive integration for health documents
import { gapi } from 'gapi-script';
import { googleConfig } from '@/config/google.config';
import { ensureValidToken } from '@/lib/googleApiHelper';

export interface DriveFile {
  id: string;
  name: string;
  mimeType: string;
  size: string;
  createdTime: string;
  modifiedTime: string;
  webViewLink: string;
  webContentLink: string;
  parents: string[];
}

export interface UploadResult {
  fileId: string;
  fileName: string;
  fileUrl: string;
  mimeType: string;
  size: number;
}

class GoogleDriveService {
  private healthFolderId: string;

  constructor() {
    this.healthFolderId = googleConfig.drive.folderId;
  }

  /**
   * Set the health folder ID
   */
  setHealthFolderId(folderId: string): void {
    this.healthFolderId = folderId;
  }

  /**
   * Get the current health folder ID
   */
  getHealthFolderId(): string {
    return this.healthFolderId;
  }

  /**
   * Ensure the health folder exists in Google Drive
   */
  async ensureHealthFolder(): Promise<string> {
    try {
      const token = await ensureValidToken();
      if (!token) {
        throw new Error('No valid authentication token available');
      }

      // Ensure Drive API is loaded
      if (!gapi.client.drive) {
        await new Promise<void>((resolve, reject) => {
          gapi.client.load('drive', 'v3').then(resolve).catch(reject);
        });
      }

      // Check if the folder already exists
      if (this.healthFolderId && this.healthFolderId !== '18oce2L5-EfDo6rY1-zzAdjTXHfZnjtnx') {
        try {
          const response = await gapi.client.drive.files.get({
            fileId: this.healthFolderId,
            fields: 'id, name, mimeType',
          });
          if (response.result && response.result.mimeType === 'application/vnd.google-apps.folder') {
            return this.healthFolderId;
          }
        } catch (error) {
          console.warn('Configured health folder not found, creating new one:', error);
        }
      }

      // Defensive: Try to find an existing folder first
      try {
        const listResponse = await gapi.client.drive.files.list({
          q: `name='Health Documents' and mimeType='application/vnd.google-apps.folder' and trashed=false`,
          fields: 'files(id, name)',
        });
        if (!listResponse || !listResponse.result || !Array.isArray(listResponse.result.files)) {
          console.error('Drive API folder list response:', listResponse);
          throw new Error('Drive API folder list response is malformed or missing');
        }
        if (listResponse.result.files.length > 0) {
          const foundFolderId = listResponse.result.files[0].id;
          this.healthFolderId = foundFolderId;
          console.log('Found existing health folder with ID:', foundFolderId);
          return foundFolderId;
        }
      } catch (listError) {
        console.error('Error listing health folders:', listError);
        throw new Error('Failed to list health folders: ' + (listError instanceof Error ? listError.message : String(listError)));
      }

      // Create a new health folder
      const folderMetadata = {
        name: 'Health Documents',
        mimeType: 'application/vnd.google-apps.folder',
        description: 'Health tracking documents and reports',
      };

      const response = await gapi.client.drive.files.create({
        resource: folderMetadata,
        fields: 'id',
      });

      if (!response.result?.id) {
        console.error('Drive API create folder response:', response);
        throw new Error('Failed to create health folder');
      }

      this.healthFolderId = response.result.id;
      console.log('Created health folder with ID:', this.healthFolderId);

      return this.healthFolderId;
    } catch (error) {
      console.error('Error ensuring health folder:', error);
      throw new Error(
        `Failed to create/access health folder: ${
          error instanceof Error ? error.message : 'Unknown error'
        }`
      );
    }
  }

  /**
   * Create a subfolder for a family member
   */
  async createFamilyMemberFolder(memberName: string): Promise<string> {
    try {
      const healthFolderId = await this.ensureHealthFolder();
      
      // Check if the subfolder already exists
      const listResponse = await gapi.client.drive.files.list({
        q: `name='${memberName}' and parents in '${healthFolderId}' and mimeType='application/vnd.google-apps.folder' and trashed=false`,
        fields: 'files(id, name)',
      });

      if (listResponse.result.files && listResponse.result.files.length > 0) {
        return listResponse.result.files[0].id!;
      }

      // Create the subfolder
      const folderMetadata = {
        name: memberName,
        mimeType: 'application/vnd.google-apps.folder',
        parents: [healthFolderId],
        description: `Health documents for ${memberName}`,
      };

      const response = await gapi.client.drive.files.create({
        resource: folderMetadata,
        fields: 'id',
      });

      if (!response.result?.id) {
        throw new Error('Failed to create family member folder');
      }

      return response.result.id;
    } catch (error) {
      console.error('Error creating family member folder:', error);
      throw new Error(`Failed to create folder for ${memberName}: ${error instanceof Error ? error.message : 'Unknown error'}`);
    }
  }

  /**
   * Upload a health document to Google Drive
   */
  async uploadHealthDocument(
    file: File,
    fileName: string,
    isForFamilyMember: boolean = false,
    familyMemberName?: string,
    description?: string
  ): Promise<UploadResult> {
    try {
      const token = await ensureValidToken();
      if (!token) {
        throw new Error('No valid authentication token available');
      }

      // Determine the parent folder
      let parentFolderId: string;
      if (isForFamilyMember && familyMemberName) {
        parentFolderId = await this.createFamilyMemberFolder(familyMemberName);
      } else {
        parentFolderId = await this.ensureHealthFolder();
      }

      // Prepare file metadata
      const metadata = {
        name: fileName,
        parents: [parentFolderId],
        description: description || 'Health document uploaded via Health Tracker',
      };

      // Create a multipart upload request
      const form = new FormData();
      form.append('metadata', new Blob([JSON.stringify(metadata)], { type: 'application/json' }));
      form.append('file', file);

      const response = await fetch('https://www.googleapis.com/upload/drive/v3/files?uploadType=multipart&fields=id,name,webViewLink,webContentLink,mimeType,size', {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`,
        },
        body: form,
      });

      if (!response.ok) {
        const errorText = await response.text();
        throw new Error(`Upload failed: ${response.status} ${response.statusText} - ${errorText}`);
      }

      const result = await response.json();

      return {
        fileId: result.id,
        fileName: result.name,
        fileUrl: result.webViewLink,
        mimeType: result.mimeType,
        size: parseInt(result.size) || file.size,
      };
    } catch (error) {
      console.error('Error uploading health document:', error);
      throw new Error(`Failed to upload document: ${error instanceof Error ? error.message : 'Unknown error'}`);
    }
  }

  /**
   * List health documents in the health folder
   */
  async listHealthDocuments(familyMemberName?: string): Promise<DriveFile[]> {
    try {
      const token = await ensureValidToken();
      if (!token) {
        throw new Error('No valid authentication token available');
      }

      let parentFolderId: string;
      if (familyMemberName) {
        parentFolderId = await this.createFamilyMemberFolder(familyMemberName);
      } else {
        parentFolderId = await this.ensureHealthFolder();
      }

      const response = await gapi.client.drive.files.list({
        q: `parents in '${parentFolderId}' and trashed=false`,
        fields: 'files(id, name, mimeType, size, createdTime, modifiedTime, webViewLink, webContentLink, parents)',
        orderBy: 'modifiedTime desc',
      });

      return response.result.files || [];
    } catch (error) {
      console.error('Error listing health documents:', error);
      throw new Error(`Failed to list documents: ${error instanceof Error ? error.message : 'Unknown error'}`);
    }
  }

  /**
   * Download a health document from Google Drive
   */
  async downloadHealthDocument(fileId: string, fileName: string): Promise<void> {
    try {
      const token = await ensureValidToken();
      if (!token) {
        throw new Error('No valid authentication token available');
      }

      // Get file metadata to check if it's a Google Workspace file
      const metadataResponse = await gapi.client.drive.files.get({
        fileId: fileId,
        fields: 'mimeType, name',
      });

      const mimeType = metadataResponse.result.mimeType;
      const isGoogleWorkspaceFile = mimeType?.includes('application/vnd.google-apps');

      let downloadUrl: string;
      
      if (isGoogleWorkspaceFile) {
        // For Google Workspace files, we need to export them
        let exportMimeType = 'application/pdf'; // Default export format
        
        if (mimeType?.includes('spreadsheet')) {
          exportMimeType = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet';
        } else if (mimeType?.includes('document')) {
          exportMimeType = 'application/vnd.openxmlformats-officedocument.wordprocessingml.document';
        } else if (mimeType?.includes('presentation')) {
          exportMimeType = 'application/vnd.openxmlformats-officedocument.presentationml.presentation';
        }
        
        downloadUrl = `https://www.googleapis.com/drive/v3/files/${fileId}/export?mimeType=${encodeURIComponent(exportMimeType)}`;
      } else {
        // For regular files, use the download endpoint
        downloadUrl = `https://www.googleapis.com/drive/v3/files/${fileId}?alt=media`;
      }

      const response = await fetch(downloadUrl, {
        headers: {
          'Authorization': `Bearer ${token}`,
        },
      });

      if (!response.ok) {
        throw new Error(`Download failed: ${response.status} ${response.statusText}`);
      }

      const blob = await response.blob();
      
      // Create download link
      const url = URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = fileName;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      URL.revokeObjectURL(url);
    } catch (error) {
      console.error('Error downloading health document:', error);
      throw new Error(`Failed to download document: ${error instanceof Error ? error.message : 'Unknown error'}`);
    }
  }

  /**
   * Delete a health document from Google Drive
   */
  async deleteHealthDocument(fileId: string): Promise<void> {
    try {
      const token = await ensureValidToken();
      if (!token) {
        throw new Error('No valid authentication token available');
      }

      await gapi.client.drive.files.delete({
        fileId: fileId,
      });
    } catch (error) {
      console.error('Error deleting health document:', error);
      throw new Error(`Failed to delete document: ${error instanceof Error ? error.message : 'Unknown error'}`);
    }
  }

  /**
   * Update document metadata
   */
  async updateDocumentMetadata(fileId: string, updates: { name?: string; description?: string }): Promise<void> {
    try {
      const token = await ensureValidToken();
      if (!token) {
        throw new Error('No valid authentication token available');
      }

      await gapi.client.drive.files.update({
        fileId: fileId,
        resource: updates,
      });
    } catch (error) {
      console.error('Error updating document metadata:', error);
      throw new Error(`Failed to update document: ${error instanceof Error ? error.message : 'Unknown error'}`);
    }
  }

  /**
   * Share a document with specific permissions
   */
  async shareDocument(fileId: string, emailAddress: string, role: 'reader' | 'writer' = 'reader'): Promise<void> {
    try {
      const token = await ensureValidToken();
      if (!token) {
        throw new Error('No valid authentication token available');
      }

      await gapi.client.drive.permissions.create({
        fileId: fileId,
        resource: {
          role: role,
          type: 'user',
          emailAddress: emailAddress,
        },
      });
    } catch (error) {
      console.error('Error sharing document:', error);
      throw new Error(`Failed to share document: ${error instanceof Error ? error.message : 'Unknown error'}`);
    }
  }
  /**
   * Get the health folder ID (creates if doesn't exist)
   */
  async getHealthFolderIdAsync(): Promise<string> {
    return await this.ensureHealthFolder();
  }
}

export const googleDriveService = new GoogleDriveService();
