import React, { useMemo } from 'react';
import { WorkoutLogEntry } from '@/types/Workout_Log';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { 
  BarChart, 
  ResponsiveContainer, 
  CartesianGrid, 
  XAxis, 
  YAxis, 
  Tooltip, 
  Legend,
  Bar,
  Cell,
} from 'recharts';
import { CHART_COLORS, COMMON_TOOLTIP_PROPS } from './ChartConfig';
import { useChartTheme } from './ChartHelpers';
import './ChartStyles.css';

interface ChartComponentProps {
  logs: WorkoutLogEntry[];
}

// Calculate weight progression by muscle group (no volume)
const calculateWeightProgressionByMuscle = (logs: WorkoutLogEntry[]) => {
  if (!logs || logs.length === 0) return [];

  // Get unique muscle groups
  const muscleGroups = [...new Set(logs.map(log => log.muscleGroup))];
  
  return muscleGroups.map(group => {
    const groupLogs = logs.filter(log => log.muscleGroup === group);
    
    if (groupLogs.length < 2) {
      return { 
        muscleGroup: group, 
        progression: 0,
      };
    }

    // Sort logs by date
    const sortedLogs = groupLogs.sort((a, b) => new Date(a.date).getTime() - new Date(b.date).getTime());
    
    // Calculate progression from first session to most recent
    const firstSession = sortedLogs.slice(0, Math.ceil(sortedLogs.length * 0.3)); // First 30% of sessions
    const recentSession = sortedLogs.slice(-Math.ceil(sortedLogs.length * 0.3)); // Last 30% of sessions
    
    const firstAvgWeight = firstSession.reduce((sum, log) => sum + log.weight, 0) / firstSession.length;
    const recentAvgWeight = recentSession.reduce((sum, log) => sum + log.weight, 0) / recentSession.length;
    
    const progression = firstAvgWeight > 0 
      ? ((recentAvgWeight - firstAvgWeight) / firstAvgWeight) * 100
      : 0;
    
    return { 
      muscleGroup: group, 
      progression: parseFloat(progression.toFixed(2)),
    };
  }).sort((a, b) => b.progression - a.progression); // Sort by progression rate
};

export const ProgressionByMuscleChart: React.FC<ChartComponentProps> = ({ logs }) => {
  const chartTheme = useChartTheme();
  
  // Calculate progression by muscle group
  const data = useMemo(() => calculateWeightProgressionByMuscle(logs), [logs]);
  
  if (!data || data.length === 0) {
    return (
      <Card className="chart-card">
        <CardHeader className="pb-3">
          <CardTitle className="text-base font-semibold">Weight Progression by Muscle Group</CardTitle>
          <CardDescription className="text-xs">No data available.</CardDescription>
        </CardHeader>
        <CardContent className="pt-0">
          <div className="chart-container flex items-center justify-center">
            <p className="text-muted-foreground text-sm">Log workouts to see muscle group weight progression.</p>
          </div>
        </CardContent>
      </Card>
    );
  }
  
  return (
    <Card className="chart-card">
      <CardHeader className="pb-3">
        <CardTitle className="text-base font-semibold">Weight Progression by Muscle Group</CardTitle>
        <CardDescription className="text-xs">Average weight progression for each muscle group over time.</CardDescription>
      </CardHeader>
      <CardContent className="pt-0">
        <div className="chart-container">
          <ResponsiveContainer width="110%" height="100%">
            <BarChart
              data={data}
              margin={{ top: 20, right: 30, left: 0, bottom: 40 }}
            >
              <CartesianGrid 
                vertical={false} 
                strokeDasharray="3 3" 
                strokeOpacity={chartTheme.gridOpacity}
                stroke={chartTheme.gridColor} 
              />
              <XAxis 
                dataKey="muscleGroup" 
                tick={{ fontSize: 12, fill: chartTheme.textColor }}
                tickLine={false} 
                axisLine={false}
                angle={-45}
                textAnchor="end" 
                height={60}
                interval={0}
              />
              <YAxis 
                unit="%" 
                tick={{ fontSize: 12, fill: chartTheme.textColor }}
                tickLine={false} 
                axisLine={false} 
              />
              <Tooltip
                formatter={(value: number) => [`${value.toFixed(2)}%`, 'Weight Progression']}
                contentStyle={COMMON_TOOLTIP_PROPS.contentStyle}
              />
              <Legend
                wrapperStyle={{ paddingTop: '8px' }}
                formatter={(value) => <span style={{ fontSize: '12px' }}>{value}</span>}
                iconSize={12}
              />
              <Bar 
                dataKey="progression" 
                name="Weight Progression Rate" 
                radius={[4, 4, 0, 0]}
                barSize={30}
                animationDuration={1500}
                animationEasing="ease-out"
              >
                {data.map((entry, index) => (
                  <Cell 
                    key={`cell-${index}`} 
                    fill={entry.progression > 0 ? CHART_COLORS.progression : CHART_COLORS.fatigue} 
                  />
                ))}
              </Bar>
            </BarChart>
          </ResponsiveContainer>
        </div>
      </CardContent>
    </Card>
  );
};
