// Utils for mapping muscle names to their corresponding icon assets
import AbdominalsIcon from '@/assets/Abdominals.png';
import BackIcon from '@/assets/back-wings.png';
import BackWingsIcon from '@/assets/back-wings.png';
import Back4Icon from '@/assets/back (4).png';
import BicepsIcon from '@/assets/biceps.png';
import CalfIcon from '@/assets/calf.png';
import ChestIcon from '@/assets/chest.png';
import ForearmIcon from '@/assets/forearm.png';
import FullBodyIcon from '@/assets/back-wings.png';
import HipIcon from '@/assets/Hip.png';
import LegIcon from '@/assets/leg.png';
import NeckIcon from '@/assets/neck.png';
import ShoulderIcon from '@/assets/shoulder.png';
import Shoulder1Icon from '@/assets/shoulder (1).png';
import TrapeziusIcon from '@/assets/trapezius.png';
import TricepsIcon from '@/assets/triceps.png';

// Mapping of muscle group names to their icon paths
export const muscleIconMap: Record<string, string> = {
  // Core muscles
  'abdominals': AbdominalsIcon,
  'abs': AbdominalsIcon,
  'core': AbdominalsIcon,
  
  // Back muscles
  'back': BackIcon,
  'lats': BackWingsIcon,
  'latissimus dorsi': BackWingsIcon,
  'rhomboids': Back4Icon,
  'middle back': Back4Icon,
  'lower back': BackIcon,
  'upper back': TrapeziusIcon,
  'trapezius': TrapeziusIcon,
  'traps': TrapeziusIcon,
  
  // Arms
  'biceps': BicepsIcon,
  'triceps': TricepsIcon,
  'forearms': ForearmIcon,
  'forearm': ForearmIcon,
  
  // Shoulders
  'shoulders': ShoulderIcon,
  'shoulder': ShoulderIcon,
  'deltoids': ShoulderIcon,
  'delts': ShoulderIcon,
  'anterior deltoid': Shoulder1Icon,
  'posterior deltoid': Shoulder1Icon,
  'lateral deltoid': Shoulder1Icon,
  
  // Chest
  'chest': ChestIcon,
  'pectorals': ChestIcon,
  'pecs': ChestIcon,
  
  // Legs
  'legs': LegIcon,
  'quadriceps': LegIcon,
  'quads': LegIcon,
  'hamstrings': LegIcon,
  'glutes': HipIcon,
  'gluteus': HipIcon,
  'hips': HipIcon,
  'calves': CalfIcon,
  'calf': CalfIcon,
  
  // Other
  'neck': NeckIcon,
  'full body': FullBodyIcon,
  'cardio': FullBodyIcon,
};

/**
 * Get the icon path for a given muscle group name
 * @param muscleName - The name of the muscle group
 * @returns The icon path or a default icon if not found
 */
export const getMuscleIcon = (muscleName: string): string => {
  // Normalize the muscle name (lowercase, trim)
  const normalizedName = muscleName.toLowerCase().trim();
  
  // Direct match
  if (muscleIconMap[normalizedName]) {
    return muscleIconMap[normalizedName];
  }
  
  // Partial match - check if any key contains the muscle name or vice versa
  for (const [key, icon] of Object.entries(muscleIconMap)) {
    if (key.includes(normalizedName) || normalizedName.includes(key)) {
      return icon;
    }
  }
  
  // Default to full body icon if no match found
  return FullBodyIcon;
};

/**
 * Check if an icon exists for a given muscle name
 * @param muscleName - The name of the muscle group
 * @returns boolean indicating if an icon exists
 */
export const hasMuscleIcon = (muscleName: string): boolean => {
  const normalizedName = muscleName.toLowerCase().trim();
  return muscleIconMap[normalizedName] !== undefined || 
         Object.keys(muscleIconMap).some(key => 
           key.includes(normalizedName) || normalizedName.includes(key)
         );
};
