import React, { useEffect, useRef } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Progress } from '@/components/ui/progress';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { useHealthStore } from '@/store/healthStore';
import { useNavigation } from '@/hooks/useConsolidatedApp';
import { formatDate } from '@/utils/healthUtils';
import { 
  Heart, 
  Apple, 
  Brain, 
  TrendingUp, 
  TrendingDown, 
  Minus,
  Weight,
  Activity
} from 'lucide-react';

export const HealthDashboardWidget: React.FC = () => {
  const { setCurrentView } = useNavigation();  const {
    nutritionGoals,
    getDailyNutrition,
    getNutritionProgress,
    getHealthTrend,
    getMeditationStats,
    fetchAllHealthData,
    isLoading,
    error,
    clearError
  } = useHealthStore();const today = formatDate(new Date());  const hasInitialized = useRef(false);
  const lastFetchTime = useRef(0);
  
  // Use a more controlled approach to prevent infinite calls
  useEffect(() => {
    const now = Date.now();
    const fiveMinutesAgo = now - 5 * 60 * 1000;
    const shouldFetch = !hasInitialized.current || 
                       (lastFetchTime.current < fiveMinutesAgo);
    
    if (shouldFetch) {
      hasInitialized.current = true;
      lastFetchTime.current = now;
      fetchAllHealthData();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []); // Empty dependency array to prevent loops

  // Get today's nutrition data
  const todayNutrition = getDailyNutrition(today);
  const nutritionProgress = getNutritionProgress(today);

  // Get health trends
  const weightTrend = getHealthTrend('weight');
  const heartRateTrend = getHealthTrend('heart_rate');

  // Get meditation stats
  const meditationStats = getMeditationStats(7);
  const getTrendIcon = (trend: number) => {
    if (trend > 2) return <TrendingUp className="h-4 w-4 text-green-500" />;
    if (trend < -2) return <TrendingDown className="h-4 w-4 text-red-500" />;
    return <Minus className="h-4 w-4 text-gray-500" />;
  };

  if (error) {
    return (
      <Card>
        <CardHeader className="flex flex-row items-center justify-between">
          <CardTitle className="text-lg font-semibold flex items-center gap-2">
            <Heart className="h-5 w-5 text-red-500" />
            Health Overview
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="text-center py-4">
            <p className="text-red-500 mb-2">{error}</p>
            <Button onClick={() => { clearError(); fetchAllHealthData(); }} size="sm">
              Retry
            </Button>
          </div>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card>
      <CardHeader className="flex flex-row items-center justify-between">
        <CardTitle className="text-lg font-semibold flex items-center gap-2">
          <Heart className="h-5 w-5 text-red-500" />
          Health Overview
        </CardTitle>
        {isLoading && (
          <div className="h-4 w-4 animate-spin rounded-full border-2 border-primary border-t-transparent" />
        )}
      </CardHeader>
      <CardContent className="space-y-4">
        {/* Quick Stats */}
        <div className="grid grid-cols-2 md:grid-cols-4 gap-3">
          <div className="text-center p-2 bg-blue-50 rounded-lg">
            <Apple className="h-5 w-5 mx-auto mb-1 text-blue-600" />
            <div className="text-lg font-semibold text-blue-700">
              {todayNutrition.calories}
            </div>
            <div className="text-xs text-blue-600">Calories</div>
          </div>
          
          <div className="text-center p-2 bg-green-50 rounded-lg">
            <Weight className="h-5 w-5 mx-auto mb-1 text-green-600" />
            <div className="text-lg font-semibold text-green-700 flex items-center justify-center gap-1">
              {Math.abs(weightTrend).toFixed(1)}%
              {getTrendIcon(weightTrend)}
            </div>
            <div className="text-xs text-green-600">Weight</div>
          </div>
          
          <div className="text-center p-2 bg-purple-50 rounded-lg">
            <Brain className="h-5 w-5 mx-auto mb-1 text-purple-600" />
            <div className="text-lg font-semibold text-purple-700">
              {meditationStats.totalSessions}
            </div>
            <div className="text-xs text-purple-600">Sessions</div>
          </div>
          
          <div className="text-center p-2 bg-orange-50 rounded-lg">
            <Activity className="h-5 w-5 mx-auto mb-1 text-orange-600" />
            <div className="text-lg font-semibold text-orange-700 flex items-center justify-center gap-1">
              {Math.abs(heartRateTrend).toFixed(1)}%
              {getTrendIcon(heartRateTrend)}
            </div>
            <div className="text-xs text-orange-600">Heart Rate</div>
          </div>
        </div>

        {/* Today's Nutrition Progress */}
        <div className="space-y-2">
          <h4 className="font-medium text-sm">Today's Nutrition</h4>
          
          <div className="space-y-2">
            <div className="flex justify-between items-center text-sm">
              <span>Calories</span>
              <span className="text-xs text-gray-500">
                {todayNutrition.calories} / {nutritionGoals.dailyCalories}
              </span>
            </div>
            <Progress value={Math.min(nutritionProgress.calories, 100)} className="h-2" />
          </div>
          
          <div className="grid grid-cols-3 gap-2 text-xs">
            <div>
              <div className="flex justify-between">
                <span>Protein</span>
                <span>{todayNutrition.protein}g</span>
              </div>
              <Progress value={Math.min(nutritionProgress.protein, 100)} className="h-1 mt-1" />
            </div>
            <div>
              <div className="flex justify-between">
                <span>Carbs</span>
                <span>{todayNutrition.carbs}g</span>
              </div>
              <Progress value={Math.min(nutritionProgress.carbs, 100)} className="h-1 mt-1" />
            </div>
            <div>
              <div className="flex justify-between">
                <span>Fat</span>
                <span>{todayNutrition.fat}g</span>
              </div>
              <Progress value={Math.min(nutritionProgress.fat, 100)} className="h-1 mt-1" />
            </div>
          </div>
        </div>

        {/* Meditation Streak */}
        {meditationStats.totalSessions > 0 && (
          <div className="flex items-center justify-between p-2 bg-indigo-50 rounded-lg">
            <div className="flex items-center gap-2">
              <Brain className="h-4 w-4 text-indigo-600" />
              <span className="text-sm font-medium">Meditation Streak</span>
            </div>
            <div className="flex items-center gap-2">
              <Badge variant="secondary">{meditationStats.streak} days</Badge>
              <span className="text-xs text-gray-500">
                {meditationStats.totalMinutes}min this week
              </span>
            </div>
          </div>
        )}        {/* Quick Actions */}
        <div className="grid grid-cols-2 gap-2 pt-2 border-t">
          <Button
            variant="outline"
            size="sm"
            onClick={() => setCurrentView('nutrition')}
            className="flex items-center gap-2"
          >
            <Apple className="h-4 w-4" />
            Log Food
          </Button>
          <Button
            variant="outline"
            size="sm"
            onClick={() => setCurrentView('health-tracking')}
            className="flex items-center gap-2"
          >
            <Activity className="h-4 w-4" />
            Log Health
          </Button>
        </div>
        
        <Button
          variant="ghost"
          size="sm"
          onClick={() => setCurrentView('meditation')}
          className="w-full flex items-center gap-2"
        >
          <Brain className="h-4 w-4" />
          Start Meditation
        </Button>
      </CardContent>
    </Card>
  );
};
