import React, { useMemo } from 'react';
import { StatCard } from '../StatCard';
import { Dumbbell } from 'lucide-react';
import { useWorkoutLogs } from '@/hooks/useConsolidatedApp';
import { getTotalWeightLifted } from '@/utils/computeDailyMetrics';
import { useMetricConversion } from '@/hooks/useMetricConversion';

export const WeightLiftedCard: React.FC = () => {
  const { workoutLogs } = useWorkoutLogs();
  const { convertAndFormatWeight } = useMetricConversion();
  const totalWeightThisMonth = useMemo(() => getTotalWeightLifted(workoutLogs, 'month'), [workoutLogs]);

  return (
    <StatCard
      title="Weight Lifted"
      value={convertAndFormatWeight(totalWeightThisMonth)}
      description="Total this month"
      icon={<Dumbbell className="w-4 h-4 text-black" />}
      trend={{
        value: 0,
        isPositive: true
      }}
    />
  );
}; 