import React from 'react';
import { PlusCircle } from 'lucide-react';
import { Card, CardContent } from '@/components/ui/card';

export const NewWorkoutActionCard: React.FC = () => {
  const handleOpenWorkoutModal = () => {
    window.dispatchEvent(new Event('open-add-workout-modal'));
  };

  return (
    <Card 
      className="transition-all hover:shadow-md hover:translate-y-[-2px] duration-300 cursor-pointer"
      onClick={handleOpenWorkoutModal}
    >
      <CardContent className="p-5">
        <div className="flex items-center gap-4">
          <div className="h-10 w-10 rounded-full bg-primary/10 flex items-center justify-center text-primary">
            <PlusCircle className="h-5 w-5" />
          </div>
          <div>
            <h4 className="font-medium">New Workout</h4>
            <p className="text-sm text-muted-foreground">Start tracking now</p>
          </div>
        </div>
      </CardContent>
    </Card>
  );
}; 