import React, { useState, useEffect, useMemo } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Plus, Heart, Activity, Thermometer, Droplets, TrendingUp, Calendar, Edit2, Trash2, FileText } from 'lucide-react';

import { useHealthStore } from '@/store/healthStore';
import { HealthEntry as StoreHealthEntry, VitalSigns as StoreVitalSigns, BodyMeasurement as StoreBodyMeasurement } from '@/types/Health';
import { PersonSelector } from '@/components/health/PersonSelector';
import { PersonManager } from '@/components/health/PersonManager';
import { FolderConfiguration } from '@/components/health/FolderConfiguration';
import { DocumentUpload } from '@/components/health/DocumentUpload';
import { DocumentList } from '@/components/health/DocumentList';
import { format } from 'date-fns';
import { toast } from "sonner";
import { useMetricConversion } from '@/hooks/useMetricConversion';
import { PageHeader } from '@/components/ui/PageHeader';
// Use the store type directly
type HealthEntry = StoreHealthEntry;

interface BodyMeasurement {
  id?: string;
  date: string;
  weight?: number;
  bodyFat?: number;
  muscleMass?: number;
  chest?: number;
  waist?: number;
  hips?: number;
  bicep?: number;
  thigh?: number;
  neck?: number;
  notes?: string;
}

interface VitalSigns {
  id?: string;
  date: string;
  systolic?: number;
  diastolic?: number;
  heartRate?: number;
  temperature?: number;
  oxygenSaturation?: number;
  notes?: string;
}

// Define local types for form state
interface BodyMeasurementFormState {
  date: string;
  weight?: number | undefined;
  bodyFat?: number | undefined;
  muscleMass?: number | undefined;
  chest?: number | undefined;
  waist?: number | undefined;
  hips?: number | undefined;
  bicep?: number | undefined;
  thigh?: number | undefined;
  neck?: number | undefined;
  notes?: string;
}

interface VitalSignsFormState {
  date: string;
  systolic?: number | undefined;
  diastolic?: number | undefined;
  heartRate?: number | undefined;
  temperature?: number | undefined;
  oxygenSaturation?: number | undefined;
  notes?: string;
}

const HealthTrackingPage: React.FC = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [showAddForm, setShowAddForm] = useState(false);
  const [editingEntry, setEditingEntry] = useState<HealthEntry | null>(null);
  const [selectedDate, setSelectedDate] = useState(format(new Date(), 'yyyy-MM-dd'));  const [activeTab, setActiveTab] = useState('overview');
  
  // Use health store first to get currentPersonId
  const { 
    healthEntries: storeHealthEntries, 
    isLoading: storeLoading, 
    isInitialized, 
    currentPersonId,
    people,
    addPerson,
    updatePerson,
    deletePerson,
    fetchAllHealthData,
    addHealthEntry,
    updateHealthEntryAction,
    deleteHealthEntryAction,
    addVitalSigns,
    addBodyMeasurement
  } = useHealthStore();
  const [formData, setFormData] = useState<HealthEntry>({
    id: '',
    personId: currentPersonId, // Use current person ID from store
    date: selectedDate,
    type: 'weight',
    value: 0,
    unit: 'kg',
    notes: '',
    timestamp: new Date().toISOString()
  });

  const [measurementForm, setMeasurementForm] = useState<BodyMeasurementFormState>({
    date: selectedDate,
    weight: undefined,
    bodyFat: undefined,
    muscleMass: undefined,
    chest: undefined,
    waist: undefined,
    hips: undefined,
    bicep: undefined,
    thigh: undefined,
    neck: undefined,
    notes: ''
  });

  const [vitalForm, setVitalForm] = useState<VitalSignsFormState>({
    date: selectedDate,
    systolic: undefined,
    diastolic: undefined,
    heartRate: undefined,
    temperature: undefined,
    oxygenSaturation: undefined,
    notes: ''
  });  const { 
    convertAndFormatWeight, 
    convertAndFormatHeight, 
    convertAndFormatTemperature, 
    getWeightUnit, 
    getHeightUnit, 
    getTemperatureUnit 
  } = useMetricConversion();

  // Dynamically set units in healthTypes
  const healthTypes = [
    { value: 'weight', label: 'Weight', unit: getWeightUnit(), icon: Activity },
    { value: 'blood_pressure', label: 'Blood Pressure', unit: 'mmHg', icon: Heart },
    { value: 'heart_rate', label: 'Heart Rate', unit: 'bpm', icon: Heart },
    { value: 'body_fat', label: 'Body Fat %', unit: '%', icon: Activity },
    { value: 'muscle_mass', label: 'Muscle Mass', unit: getWeightUnit(), icon: Activity },
    { value: 'temperature', label: 'Temperature', unit: getTemperatureUnit() === 'celsius' ? 'C' : 'F', icon: Thermometer },
    { value: 'sleep_hours', label: 'Sleep Hours', unit: 'hours', icon: Activity },
    { value: 'water_intake', label: 'Water Intake', unit: 'ml', icon: Droplets },
    { value: 'steps', label: 'Steps', unit: 'steps', icon: TrendingUp },
    { value: 'custom', label: 'Custom', unit: '', icon: Activity }
  ];// Filter entries for selected date and current person using useMemo to prevent infinite re-renders
  const healthEntries = useMemo(() => 
    storeHealthEntries.filter(entry => entry.date === selectedDate && entry.personId === currentPersonId),
    [storeHealthEntries, selectedDate, currentPersonId]
  );

  useEffect(() => {
    if (!isInitialized) {
      fetchAllHealthData();
    }
  }, [isInitialized, fetchAllHealthData]);
  // Update local loading state based on store loading
  useEffect(() => {
    setIsLoading(storeLoading);
  }, [storeLoading]);

  // Update form data when currentPersonId changes
  useEffect(() => {
    setFormData(prev => ({
      ...prev,
      personId: currentPersonId
    }));
  }, [currentPersonId]);

  // Update form data when selectedDate changes
  useEffect(() => {
    setFormData(prev => ({
      ...prev,
      date: selectedDate
    }));
    setMeasurementForm(prev => ({
      ...prev,
      date: selectedDate
    }));
    setVitalForm(prev => ({
      ...prev,
      date: selectedDate
    }));
  }, [selectedDate]);
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);
    
    try {      if (editingEntry?.id) {
        await updateHealthEntryAction(editingEntry.id, formData);
        toast.success("Health entry updated successfully!");
      } else {
        await addHealthEntry(formData);
        toast.success("Health entry added successfully!");
      }      resetForm();
      // Data will be automatically updated in the store
    } catch (error) {
      console.error('Error saving health entry:', error);
      toast.error("Failed to save health entry. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  const handleEdit = (entry: HealthEntry) => {
    setEditingEntry(entry);
    setFormData(entry);
    setShowAddForm(true);
  };  const handleDelete = async (entry: HealthEntry) => {
    if (!confirm('Are you sure you want to delete this entry?')) return;

    setIsLoading(true);
    try {      if (entry.id) {
        await deleteHealthEntryAction(entry.id);
        toast.success("Health entry deleted successfully!");        // Data will be automatically updated in the store
      }
    } catch (error) {
      console.error('Error deleting health entry:', error);
      toast.error("Failed to delete health entry. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };  const resetForm = () => {
    setFormData({
      id: '',
      personId: currentPersonId,
      date: selectedDate,
      type: 'weight',
      value: 0,
      unit: '',
      notes: '',
      timestamp: new Date().toISOString()
    });
    setEditingEntry(null);
    setShowAddForm(false);
    setMeasurementForm({
      date: selectedDate,
      weight: undefined,
      bodyFat: undefined,
      muscleMass: undefined,
      chest: undefined,
      waist: undefined,
      hips: undefined,
      bicep: undefined,
      thigh: undefined,
      neck: undefined,
      notes: ''
    });
    setVitalForm({
      date: selectedDate,
      systolic: undefined,
      diastolic: undefined,
      heartRate: undefined,
      temperature: undefined,
      oxygenSaturation: undefined,
      notes: ''
    });
  };  const handleSaveVitalSigns = async () => {
    setIsLoading(true);
    
    try {      const vitalData: Partial<Omit<StoreVitalSigns, 'id' | 'timestamp'>> = {
        personId: currentPersonId,
        date: vitalForm.date,
        time: new Date().toTimeString().split(' ')[0], // Current time in HH:mm:ss format
      };

      // Only include fields that have values
      if (vitalForm.systolic !== undefined) vitalData.bloodPressureSystolic = vitalForm.systolic;
      if (vitalForm.diastolic !== undefined) vitalData.bloodPressureDiastolic = vitalForm.diastolic;
      if (vitalForm.heartRate !== undefined) vitalData.heartRate = vitalForm.heartRate;
      if (vitalForm.temperature !== undefined) {
        const temp = vitalForm.temperature;
        vitalData.temperature = getTemperatureUnit() === 'fahrenheit' ? (temp - 32) * 5/9 : temp;
      }
      if (vitalForm.oxygenSaturation !== undefined) vitalData.oxygenSaturation = vitalForm.oxygenSaturation;
      if (vitalForm.notes && vitalForm.notes.trim()) vitalData.notes = vitalForm.notes;

      const success = await addVitalSigns(vitalData as Omit<StoreVitalSigns, 'id' | 'timestamp'>);
        if (success) {
        toast.success("Vital signs saved successfully!");
        
        // Reset form
        setVitalForm({
          date: selectedDate,
          systolic: undefined,
          diastolic: undefined,
          heartRate: undefined,
          temperature: undefined,
          oxygenSaturation: undefined,
          notes: ''
        });
      } else {        throw new Error('Failed to save vital signs');
      }
    } catch (error) {
      console.error('Error saving vital signs:', error);
      toast.error("Failed to save vital signs. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  const handleSaveBodyMeasurements = async () => {
    setIsLoading(true);
    
    try {      const measurementData: Partial<Omit<StoreBodyMeasurement, 'id' | 'timestamp'>> = {
        personId: currentPersonId,
        date: measurementForm.date,
        time: new Date().toTimeString().split(' ')[0], // Current time in HH:mm:ss format
      };

      // Only include fields that have values
      if (measurementForm.weight !== undefined) {
        const weight = measurementForm.weight;
        measurementData.weight = getWeightUnit() === 'lbs' ? weight * 0.453592 : weight;
      }
      if (measurementForm.bodyFat !== undefined) measurementData.bodyFat = measurementForm.bodyFat;
      if (measurementForm.muscleMass !== undefined) {
        const muscleMass = measurementForm.muscleMass;
        measurementData.muscleMass = getWeightUnit() === 'lbs' ? muscleMass * 0.453592 : muscleMass;
      }
      if (measurementForm.chest !== undefined) measurementData.chest = measurementForm.chest;
      if (measurementForm.waist !== undefined) measurementData.waist = measurementForm.waist;
      if (measurementForm.hips !== undefined) measurementData.hips = measurementForm.hips;
      if (measurementForm.neck !== undefined) measurementData.neck = measurementForm.neck;
      if (measurementForm.notes && measurementForm.notes.trim()) measurementData.notes = measurementForm.notes;

      const success = await addBodyMeasurement(measurementData as Omit<StoreBodyMeasurement, 'id' | 'timestamp'>);
        if (success) {
        toast.success("Body measurements saved successfully!");
        
        // Reset form
        setMeasurementForm({
          date: selectedDate,
          weight: undefined,
          bodyFat: undefined,
          muscleMass: undefined,
          chest: undefined,
          waist: undefined,
          hips: undefined,
          bicep: undefined,
          thigh: undefined,
          neck: undefined,
          notes: ''
        });
      } else {        throw new Error('Failed to save body measurements');
      }
    } catch (error) {
      console.error('Error saving body measurements:', error);
      toast.error("Failed to save body measurements. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  const getEntryTypeIcon = (type: string) => {
    const healthType = healthTypes.find(t => t.value === type);
    const IconComponent = healthType?.icon || Activity;
    return <IconComponent className="h-4 w-4" />;
  };

  const getEntryTypeColor = (type: string) => {
    switch (type) {
      case 'weight': return 'bg-blue-100 text-blue-800';
      case 'blood-pressure': return 'bg-red-100 text-red-800';
      case 'heart-rate': return 'bg-pink-100 text-pink-800';
      case 'body-fat': return 'bg-green-100 text-green-800';
      case 'measurements': return 'bg-purple-100 text-purple-800';
      case 'symptoms': return 'bg-orange-100 text-orange-800';
      case 'medication': return 'bg-teal-100 text-teal-800';
      case 'mood': return 'bg-yellow-100 text-yellow-800';
      case 'energy': return 'bg-indigo-100 text-indigo-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const getLatestEntry = (type: string) => {
    return healthEntries
      .filter(entry => entry.type === type)
      .sort((a, b) => new Date(b.timestamp).getTime() - new Date(a.timestamp).getTime())[0];
  };  return (
    <div className="p-2 sm:p-4 md:p-6 space-y-4 md:space-y-6 min-h-[80vh]">
      <PageHeader
        icon={Heart}
        title="Health Tracking"
        date={selectedDate}
        onDateChange={setSelectedDate}
        action={
          <div className="flex flex-col sm:flex-row items-stretch sm:items-center gap-2 sm:gap-3">
            <PersonSelector aria-label="Select person" />
            <PersonManager
              people={people}
              onPersonAdd={addPerson}
              onPersonUpdate={updatePerson}
              onPersonDelete={deletePerson}
            />
            <Button
              onClick={() => setShowAddForm(true)}
              className="flex items-center justify-center gap-2 focus-visible:ring-2 focus-visible:ring-primary"
              aria-label="Add Health Entry"
            >
              <Plus className="h-4 w-4" />
              <span className="hidden sm:inline">Add Health Entry</span>
              <span className="sm:hidden">Add Entry</span>
            </Button>
          </div>
        }
      />

      <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
        <TabsList className="grid w-full grid-cols-2 sm:grid-cols-5 h-15 sm:h-auto">
          <TabsTrigger value="overview" className="text-xs sm:text-sm">Overview</TabsTrigger>
          <TabsTrigger value="vitals" className="text-xs sm:text-sm">Vitals</TabsTrigger>
          <TabsTrigger value="measurements" className="text-xs sm:text-sm">Measurements</TabsTrigger>
          <TabsTrigger value="documents" className="text-xs sm:text-sm">Documents</TabsTrigger>
          <TabsTrigger value="analytics" className="text-xs sm:text-sm">Analytics</TabsTrigger>
        </TabsList>        <TabsContent value="overview" className="space-y-4 md:space-y-6">
          {/* Quick Stats */}
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-3 md:gap-4">
            {['weight', 'blood-pressure', 'heart-rate', 'body-fat'].map((type) => {
              const entry = getLatestEntry(type);
              const healthType = healthTypes.find(t => t.value === type);
              
              return (
                <Card key={type}>
                  <CardContent className="p-3 md:p-4">
                    <div className="flex items-center justify-between">
                      <div className="flex items-center gap-2">
                        {getEntryTypeIcon(type)}
                        <span className="font-medium text-xs sm:text-sm">{healthType?.label}</span>
                      </div>
                      <Badge variant="outline" className={`${getEntryTypeColor(type)} text-xs`}>
                        Latest
                      </Badge>
                    </div>
                    <div className="mt-2">
                      {entry ? (
                        <div>
                          <span className="text-xl md:text-2xl font-bold">{
                            healthType?.value === 'weight' ? convertAndFormatWeight(entry.value) :
                            healthType?.value === 'muscle_mass' ? convertAndFormatWeight(entry.value) :
                            healthType?.value === 'temperature' ? convertAndFormatTemperature(entry.value) :
                            healthType?.value === 'body_fat' ? `${entry.value}%` :
                            entry.value
                          }</span>
                          <span className="text-xs sm:text-sm text-gray-500 ml-1">{entry.unit}</span>
                          <div className="text-xs text-gray-500 mt-1">
                            {format(new Date(entry.timestamp), 'MMM d, h:mm a')}
                          </div>
                        </div>
                      ) : (
                        <span className="text-gray-400 text-sm">No data</span>
                      )}
                    </div>
                  </CardContent>
                </Card>
              );
            })}
          </div>

          {/* Today's Entries */}
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Calendar className="h-5 w-5" />
                Today's Health Entries
              </CardTitle>
            </CardHeader>
            <CardContent>
              {healthEntries.length === 0 ? (
                <div className="text-center py-8">
                  <Activity className="mx-auto h-12 w-12 text-gray-400" />
                  <h3 className="mt-2 text-sm font-medium text-gray-900">No entries today</h3>
                  <p className="mt-1 text-sm text-gray-500">
                    Start tracking your health by adding your first entry.
                  </p>
                </div>              ) : (
                <div className="space-y-4">
                  {healthEntries.map((entry, index) => {
                    const healthType = healthTypes.find(t => t.value === entry.type);
                    return (
                      <div key={index} className="flex flex-col sm:flex-row sm:items-center justify-between p-3 md:p-4 border border-gray-200 rounded-lg gap-3">
                        <div className="flex-1">
                          <div className="flex items-center gap-2 sm:gap-3 flex-wrap">
                            {getEntryTypeIcon(entry.type)}
                            <Badge className={`${getEntryTypeColor(entry.type)} text-xs`}>
                              {healthTypes.find(t => t.value === entry.type)?.label}
                            </Badge>
                            <span className="font-medium text-sm sm:text-base">{
                              healthType?.value === 'weight' ? convertAndFormatWeight(entry.value) :
                              healthType?.value === 'muscle_mass' ? convertAndFormatWeight(entry.value) :
                              healthType?.value === 'temperature' ? convertAndFormatTemperature(entry.value) :
                              healthType?.value === 'body_fat' ? `${entry.value}%` :
                              entry.value
                            } {entry.unit}</span>
                          </div>
                          <div className="mt-1 text-xs sm:text-sm text-gray-600">
                            {format(new Date(entry.timestamp), 'h:mm a')}
                          </div>
                          {entry.notes && (
                            <p className="mt-1 text-xs sm:text-sm text-gray-500">{entry.notes}</p>
                          )}
                        </div>
                        <div className="flex items-center gap-2 self-end sm:self-auto">
                          <Button
                            variant="outline"
                            size="sm"
                            onClick={() => handleEdit(entry)}
                            className="h-8"
                          >
                            <Edit2 className="h-3 w-3 sm:h-4 sm:w-4" />
                          </Button>                          <Button
                            variant="outline"
                            size="sm"
                            onClick={() => handleDelete(entry)}
                            className="h-8"
                          >
                            <Trash2 className="h-3 w-3 sm:h-4 sm:w-4" />
                          </Button>
                        </div>
                      </div>
                    );
                  })}
                </div>
              )}
            </CardContent>
          </Card>
        </TabsContent>        <TabsContent value="vitals">
          <Card>
            <CardHeader>
              <CardTitle className="text-lg sm:text-xl">Vital Signs</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4 sm:gap-6">
                <div className="space-y-3 sm:space-y-4">
                  <h3 className="font-medium flex items-center gap-2 text-sm sm:text-base">
                    <Heart className="h-4 w-4 text-red-500" />
                    Blood Pressure
                  </h3>
                  <div className="space-y-2">
                    <div className="flex flex-col sm:flex-row gap-2">
                      <Input
                        placeholder="Systolic"
                        type="number"
                        value={vitalForm.systolic || ''}
                        onChange={(e) => setVitalForm({...vitalForm, systolic: Number(e.target.value)})}
                        className="text-sm"
                      />
                      <Input
                        placeholder="Diastolic"
                        type="number"
                        value={vitalForm.diastolic || ''}
                        onChange={(e) => setVitalForm({...vitalForm, diastolic: Number(e.target.value)})}
                        className="text-sm"
                      />
                    </div>
                    <div className="text-xs sm:text-sm text-gray-500">mmHg</div>
                  </div>
                </div>

                <div className="space-y-3 sm:space-y-4">
                  <h3 className="font-medium flex items-center gap-2 text-sm sm:text-base">
                    <Heart className="h-4 w-4 text-pink-500" />
                    Heart Rate
                  </h3>
                  <div className="space-y-2">
                    <Input
                      placeholder="Heart Rate"
                      type="number"
                      value={vitalForm.heartRate || ''}
                      onChange={(e) => setVitalForm({...vitalForm, heartRate: Number(e.target.value)})}
                      className="text-sm"
                    />
                    <div className="text-xs sm:text-sm text-gray-500">bpm</div>
                  </div>
                </div>

                <div className="space-y-3 sm:space-y-4">
                  <h3 className="font-medium flex items-center gap-2 text-sm sm:text-base">
                    <Thermometer className="h-4 w-4 text-orange-500" />
                    Temperature
                  </h3>                  <div className="space-y-2">
                    <Label htmlFor="temperature" className="text-sm">Temperature ({getTemperatureUnit() === 'celsius' ? 'C' : 'F'})</Label>
                    <Input
                      id="temperature"
                      placeholder="Temperature"
                      type="number"
                      step="0.1"
                      value={vitalForm.temperature || ''}
                      onChange={(e) => setVitalForm({...vitalForm, temperature: Number(e.target.value)})}
                      className="text-sm"
                    />
                  </div>
                </div>

                <div className="space-y-3 sm:space-y-4">
                  <h3 className="font-medium flex items-center gap-2 text-sm sm:text-base">
                    <Droplets className="h-4 w-4 text-blue-500" />
                    Oxygen Saturation
                  </h3>
                  <div className="space-y-2">
                    <Input
                      placeholder="O2 Saturation"
                      type="number"
                      value={vitalForm.oxygenSaturation || ''}
                      onChange={(e) => setVitalForm({...vitalForm, oxygenSaturation: Number(e.target.value)})}
                      className="text-sm"
                    />
                    <div className="text-xs sm:text-sm text-gray-500">%</div>
                  </div>
                </div>
              </div>

              <div className="mt-4 sm:mt-6">
                <Label htmlFor="vitalNotes" className="text-sm sm:text-base">Notes</Label>
                <Textarea
                  id="vitalNotes"
                  value={vitalForm.notes}
                  onChange={(e) => setVitalForm({...vitalForm, notes: e.target.value})}
                  placeholder="Any additional notes about your vital signs..."
                  rows={3}
                  className="text-sm"
                />
              </div>              <Button className="mt-4 w-full sm:w-auto" onClick={handleSaveVitalSigns} disabled={isLoading}>
                {isLoading ? 'Saving...' : 'Save Vital Signs'}
              </Button>
            </CardContent>
          </Card>
        </TabsContent>        <TabsContent value="measurements">
          <Card>
            <CardHeader>
              <CardTitle className="text-lg sm:text-xl">Body Measurements</CardTitle>
            </CardHeader><CardContent>
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4 sm:gap-6">
                <div className="space-y-3 sm:space-y-4">
                  <h3 className="font-medium text-sm sm:text-base">Body Composition</h3>
                  <div className="space-y-3">                    <div>
                      <Label htmlFor="weight" className="text-sm">Weight ({getWeightUnit()})</Label>
                      <Input
                        id="weight"
                        type="number"
                        step="0.1"
                        value={measurementForm.weight || ''}
                        onChange={(e) => setMeasurementForm({...measurementForm, weight: Number(e.target.value)})}
                        className="text-sm"
                      />
                    </div>
                    <div>
                      <Label htmlFor="bodyFat" className="text-sm">Body Fat (%)</Label>
                      <Input
                        id="bodyFat"
                        type="number"
                        step="0.1"
                        value={measurementForm.bodyFat || ''}
                        onChange={(e) => setMeasurementForm({...measurementForm, bodyFat: Number(e.target.value)})}
                        className="text-sm"
                      />
                    </div>                    <div>
                      <Label htmlFor="muscleMass" className="text-sm">Muscle Mass ({getWeightUnit()})</Label>
                      <Input
                        id="muscleMass"
                        type="number"
                        step="0.1"
                        value={measurementForm.muscleMass || ''}
                        onChange={(e) => setMeasurementForm({...measurementForm, muscleMass: Number(e.target.value)})}
                        className="text-sm"
                      />
                    </div>
                  </div>
                </div>

                <div className="space-y-3 sm:space-y-4">
                  <h3 className="font-medium text-sm sm:text-base">Upper Body</h3>
                  <div className="space-y-3">                    <div>
                      <Label htmlFor="chest" className="text-sm">Chest ({getHeightUnit()})</Label>
                      <Input
                        id="chest"
                        type="number"
                        step="0.1"
                        value={measurementForm.chest || ''}
                        onChange={(e) => setMeasurementForm({...measurementForm, chest: Number(e.target.value)})}
                        className="text-sm"
                      />
                    </div>
                    <div>
                      <Label htmlFor="bicep" className="text-sm">Bicep ({getHeightUnit()})</Label>
                      <Input
                        id="bicep"
                        type="number"
                        step="0.1"
                        value={measurementForm.bicep || ''}
                        onChange={(e) => setMeasurementForm({...measurementForm, bicep: Number(e.target.value)})}
                        className="text-sm"
                      />
                    </div>
                    <div>
                      <Label htmlFor="neck" className="text-sm">Neck ({getHeightUnit()})</Label>
                      <Input
                        id="neck"
                        type="number"
                        step="0.1"
                        value={measurementForm.neck || ''}
                        onChange={(e) => setMeasurementForm({...measurementForm, neck: Number(e.target.value)})}
                        className="text-sm"
                      />
                    </div>
                  </div>
                </div>

                <div className="space-y-3 sm:space-y-4">
                  <h3 className="font-medium text-sm sm:text-base">Lower Body</h3>
                  <div className="space-y-3">                    <div>
                      <Label htmlFor="waist" className="text-sm">Waist ({getHeightUnit()})</Label>
                      <Input
                        id="waist"
                        type="number"
                        step="0.1"
                        value={measurementForm.waist || ''}
                        onChange={(e) => setMeasurementForm({...measurementForm, waist: Number(e.target.value)})}
                        className="text-sm"
                      />
                    </div>
                    <div>
                      <Label htmlFor="hips" className="text-sm">Hips ({getHeightUnit()})</Label>
                      <Input
                        id="hips"
                        type="number"
                        step="0.1"
                        value={measurementForm.hips || ''}
                        onChange={(e) => setMeasurementForm({...measurementForm, hips: Number(e.target.value)})}
                        className="text-sm"
                      />
                    </div>
                    <div>
                      <Label htmlFor="thigh" className="text-sm">Thigh ({getHeightUnit()})</Label>
                      <Input
                        id="thigh"
                        type="number"
                        step="0.1"
                        value={measurementForm.thigh || ''}
                        onChange={(e) => setMeasurementForm({...measurementForm, thigh: Number(e.target.value)})}
                        className="text-sm"
                      />
                    </div>
                  </div>
                </div>
              </div>

              <div className="mt-4 sm:mt-6">
                <Label htmlFor="measurementNotes" className="text-sm sm:text-base">Notes</Label>
                <Textarea
                  id="measurementNotes"
                  value={measurementForm.notes}
                  onChange={(e) => setMeasurementForm({...measurementForm, notes: e.target.value})}
                  placeholder="Any additional notes about your measurements..."
                  rows={3}
                  className="text-sm"
                />
              </div>              <Button className="mt-4 w-full sm:w-auto" onClick={handleSaveBodyMeasurements} disabled={isLoading}>
                {isLoading ? 'Saving...' : 'Save Measurements'}
              </Button>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="documents" className="space-y-4 md:space-y-6">
          {/* Folder Configuration */}
          <FolderConfiguration />
          
          {/* Document Upload */}
          <DocumentUpload />
          
          {/* Document List */}
          <DocumentList />
        </TabsContent>

        <TabsContent value="analytics">
          <Card>
            <CardHeader>
              <CardTitle>Health Analytics</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-center py-8">
                <TrendingUp className="mx-auto h-12 w-12 text-gray-400" />
                <h3 className="mt-2 text-sm font-medium text-gray-900">Health Analytics</h3>
                <p className="mt-1 text-sm text-gray-500">
                  View trends and insights from your health data over time.
                </p>
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>      {/* Add/Edit Form Modal */}
      {showAddForm && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-3 sm:p-4 z-50">
          <Card className="w-full max-w-sm sm:max-w-md max-h-[95vh] sm:max-h-[90vh] overflow-y-auto">
            <CardHeader className="pb-3 sm:pb-6">
              <CardTitle className="text-lg sm:text-xl">
                {editingEntry ? 'Edit Health Entry' : 'Add Health Entry'}
              </CardTitle>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSubmit} className="space-y-3 sm:space-y-4">
                <div>
                  <Label htmlFor="type" className="text-sm sm:text-base">Type</Label>                  <Select 
                    value={formData.type} 
                    onValueChange={(value: HealthEntry['type']) => {
                      const healthType = healthTypes.find(t => t.value === value);
                      setFormData({ 
                        ...formData, 
                        type: value,
                        unit: healthType?.unit || ''
                      });
                    }}
                  >
                    <SelectTrigger className="text-sm">
                      <SelectValue placeholder="Select health metric" />
                    </SelectTrigger>
                    <SelectContent>
                      {healthTypes.map((type) => (
                        <SelectItem key={type.value} value={type.value} className="text-sm">
                          {type.label}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>

                <div>
                  <Label htmlFor="value" className="text-sm sm:text-base">Value</Label>
                  <div className="flex gap-2">
                    <Input
                      id="value"
                      type="number"
                      value={formData.value}
                      onChange={(e) => setFormData({ ...formData, value: Number(e.target.value) })}
                      placeholder="Enter value"
                      required
                      className="flex-1 text-sm"
                    />
                    {formData.unit && (
                      <div className="flex items-center px-2 sm:px-3 border border-gray-300 rounded-md bg-gray-50">
                        <span className="text-xs sm:text-sm text-gray-600">{formData.unit}</span>
                      </div>
                    )}
                  </div>
                </div>

                <div>
                  <Label htmlFor="notes" className="text-sm sm:text-base">Notes (optional)</Label>
                  <Textarea
                    id="notes"
                    value={formData.notes}
                    onChange={(e) => setFormData({ ...formData, notes: e.target.value })}
                    placeholder="Any additional notes..."
                    rows={3}
                    className="text-sm"
                  />
                </div>

                <div className="flex flex-col sm:flex-row gap-2 pt-3 sm:pt-4">
                  <Button type="submit" disabled={isLoading} className="flex-1 text-sm sm:text-base">
                    {isLoading ? 'Saving...' : editingEntry ? 'Update Entry' : 'Add Entry'}
                  </Button>
                  <Button type="button" variant="outline" onClick={resetForm} className="text-sm sm:text-base">
                    Cancel
                  </Button>
                </div>
              </form>
            </CardContent>
          </Card>
        </div>
      )}
    </div>
  );
};

export default HealthTrackingPage;
