import React from 'react';
import { DashboardCustomizationToolbar } from './DashboardCustomizationToolbar';
import { CustomizableDashboardGrid } from './CustomizableDashboardGrid';
import { AddWorkoutButton } from './cards';
import { StaggerContainer, StaggerItem, SlideUp } from '@/components/ui/animations';

export const DashboardMainContent: React.FC = () => {
  return (
    <StaggerContainer className="flex flex-col gap-2 sm:gap-4 md:gap-6 px-2 sm:px-0">
      {/* Dashboard Header */}
      <StaggerItem>
        <SlideUp>
          <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-2 sm:gap-3 p-2 sm:p-3 md:p-4">
            <h1 className="text-xl sm:text-2xl font-semibold text-foreground">Dashboard</h1>
            <AddWorkoutButton />
          </div>
        </SlideUp>
      </StaggerItem>

      {/* Dashboard Customization Toolbar */}
      <StaggerItem>
        <SlideUp delay={0.05}>
          <DashboardCustomizationToolbar />
        </SlideUp>
      </StaggerItem>

      {/* Customizable Dashboard Grid */}
      <StaggerItem>
        <SlideUp delay={0.1}>
          <div className="relative">
            <CustomizableDashboardGrid />
          </div>
        </SlideUp>
      </StaggerItem>
    </StaggerContainer>
  );
};
