import React, { useState, useEffect, useMemo } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Plus, Utensils, TrendingUp, Calendar, Search, Edit2, Trash2 } from 'lucide-react';
import { toast } from 'sonner';
import { useHealthStore } from '@/store/healthStore';
import { NutritionEntry as StoreNutritionEntry } from '@/types/Health';
import { format } from 'date-fns';
import { PageHeader } from '@/components/ui/PageHeader';

// Use store type directly
type NutritionEntry = StoreNutritionEntry;

interface DailyTotals {
  calories: number;
  protein: number;
  carbs: number;
  fat: number;
  fiber: number;
}

const NutritionPage: React.FC = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [showAddForm, setShowAddForm] = useState(false);  const [editingEntry, setEditingEntry] = useState<NutritionEntry | null>(null);
  const [selectedDate, setSelectedDate] = useState(format(new Date(), 'yyyy-MM-dd'));
  const [searchTerm, setSearchTerm] = useState('');
  const [formData, setFormData] = useState<NutritionEntry>({
    id: '',
    personId: 'self',
    date: selectedDate,
    meal: 'breakfast',
    food: '',
    calories: 0,
    protein: 0,
    carbs: 0,
    fat: 0,
    fiber: 0,
    notes: '',
    timestamp: new Date().toISOString()
  });
  const dailyGoals = {
    calories: 2000,
    protein: 150,
    carbs: 250,
    fat: 65,
    fiber: 25
  };
  // Use health store instead of direct API calls
  const { 
    nutritionEntries, 
    isLoading: storeLoading, 
    isInitialized, 
    fetchAllHealthData,
    addNutritionEntry,
    updateNutritionEntryAction,
    deleteNutritionEntryAction
  } = useHealthStore();
  // Filter entries for selected date using memoized computed value
  const entries = useMemo(() => 
    nutritionEntries.filter(entry => entry.date === selectedDate),
    [nutritionEntries, selectedDate]
  );

  useEffect(() => {
    if (!isInitialized) {
      fetchAllHealthData();
    }
  }, [isInitialized, fetchAllHealthData]);

  // Update local loading state based on store loading
  useEffect(() => {
    setIsLoading(storeLoading);
  }, [storeLoading]);

  const calculateDailyTotals = (): DailyTotals => {
    return entries.reduce(
      (totals, entry) => ({
        calories: totals.calories + entry.calories,
        protein: totals.protein + entry.protein,
        carbs: totals.carbs + entry.carbs,
        fat: totals.fat + entry.fat,
        fiber: totals.fiber + entry.fiber,
      }),
      { calories: 0, protein: 0, carbs: 0, fat: 0, fiber: 0 }
    );
  };
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);    try {      if (editingEntry?.id) {
        await updateNutritionEntryAction(editingEntry.id, formData);
        toast.success("Nutrition entry updated successfully!");
      } else {
        await addNutritionEntry(formData);
        toast.success("Nutrition entry added successfully!");
      }
        resetForm();
      // Data will be automatically updated in the store
    } catch (error) {
      console.error('Error saving nutrition entry:', error);
      toast.error("Failed to save nutrition entry. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  const handleEdit = (entry: NutritionEntry) => {
    setEditingEntry(entry);
    setFormData(entry);
    setShowAddForm(true);
  };  const handleDelete = async (entry: NutritionEntry) => {
    if (!confirm('Are you sure you want to delete this entry?')) return;

    setIsLoading(true);
    try {      if (entry.id) {
        await deleteNutritionEntryAction(entry.id);
        toast.success("Nutrition entry deleted successfully!");
        // Data will be automatically updated in the store
      }
    } catch (error) {
      console.error('Error deleting nutrition entry:', error);
      toast.error("Failed to delete nutrition entry. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  const resetForm = () => {    setFormData({
      id: '',
      personId: 'self',
      date: selectedDate,
      meal: 'breakfast',
      food: '',
      calories: 0,
      protein: 0,
      carbs: 0,
      fat: 0,
      fiber: 0,
      notes: '',
      timestamp: new Date().toISOString()
    });
    setEditingEntry(null);
    setShowAddForm(false);  };

  const searchFilteredEntries = entries.filter(entry =>
    entry.food.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const dailyTotals = calculateDailyTotals();

  const getMealColor = (meal: string) => {
    switch (meal) {
      case 'breakfast': return 'bg-orange-100 text-orange-800';
      case 'lunch': return 'bg-green-100 text-green-800';
      case 'dinner': return 'bg-blue-100 text-blue-800';
      case 'snack': return 'bg-purple-100 text-purple-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const getProgressPercentage = (current: number, goal: number) => {
    return Math.min((current / goal) * 100, 100);
  };

  return (
    <div className="p-2 sm:p-4 md:p-6 space-y-4 md:space-y-6 min-h-[80vh]">
      <PageHeader
        icon={Utensils}
        title="Nutrition Tracking"
        date={selectedDate}
        onDateChange={setSelectedDate}
        action={
          <Button onClick={() => setShowAddForm(true)} aria-label="Add Food Entry">
            <Plus className="h-4 w-4" /> Add Food Entry
          </Button>
        }
      />

      <Tabs defaultValue="overview" className="w-full">
        <TabsList className="grid w-full grid-cols-3">
          <TabsTrigger value="overview">Overview</TabsTrigger>
          <TabsTrigger value="entries">Food Entries</TabsTrigger>
          <TabsTrigger value="analytics">Analytics</TabsTrigger>
        </TabsList>

        <TabsContent value="overview" className="space-y-6">
          {/* Daily Totals */}
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <TrendingUp className="h-5 w-5" />
                Daily Nutrition Summary
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-1 md:grid-cols-5 gap-4">
                {Object.entries(dailyTotals).map(([key, value]) => {
                  const goal = dailyGoals[key as keyof DailyTotals];
                  const percentage = getProgressPercentage(value, goal);
                  
                  return (
                    <div key={key} className="space-y-2">
                      <div className="flex justify-between text-sm">
                        <span className="capitalize font-medium">{key}</span>
                        <span className="text-gray-600">
                          {Math.round(value)}/{goal}{key === 'calories' ? '' : 'g'}
                        </span>
                      </div>
                      <div className="w-full bg-gray-200 rounded-full h-2">
                        <div
                          className="bg-blue-600 h-2 rounded-full transition-all duration-300"
                          style={{ width: `${percentage}%` }}
                        />
                      </div>
                      <div className="text-xs text-gray-500">
                        {Math.round(percentage)}% of goal
                      </div>
                    </div>
                  );
                })}
              </div>
            </CardContent>
          </Card>

          {/* Recent Entries */}
          <Card>
            <CardHeader>
              <CardTitle>Today's Food Entries</CardTitle>
            </CardHeader>
            <CardContent>
              {searchFilteredEntries.length === 0 ? (
                <div className="text-center py-8">
                  <Utensils className="mx-auto h-12 w-12 text-gray-400" />
                  <h3 className="mt-2 text-sm font-medium text-gray-900">No food entries</h3>
                  <p className="mt-1 text-sm text-gray-500">
                    Start tracking your nutrition by adding your first food entry.
                  </p>
                </div>
              ) : (
                <div className="space-y-4">
                  {searchFilteredEntries.map((entry, index) => (
                    <div key={index} className="flex items-center justify-between p-4 border border-gray-200 rounded-lg">
                      <div className="flex-1">
                        <div className="flex items-center gap-3">
                          <Badge className={getMealColor(entry.meal)}>
                            {entry.meal.charAt(0).toUpperCase() + entry.meal.slice(1)}
                          </Badge>
                          <h3 className="font-medium">{entry.food}</h3>
                        </div>
                        <div className="mt-2 text-sm text-gray-600 grid grid-cols-2 md:grid-cols-5 gap-2">
                          <span>{entry.calories} cal</span>
                          <span>{entry.protein}g protein</span>
                          <span>{entry.carbs}g carbs</span>
                          <span>{entry.fat}g fat</span>
                          <span>{entry.fiber}g fiber</span>
                        </div>
                        {entry.notes && (
                          <p className="mt-1 text-sm text-gray-500">{entry.notes}</p>
                        )}
                      </div>
                      <div className="flex items-center gap-2">
                        <Button
                          variant="outline"
                          size="sm"
                          onClick={() => handleEdit(entry)}
                        >
                          <Edit2 className="h-4 w-4" />
                        </Button>                        <Button
                          variant="outline"
                          size="sm"
                          onClick={() => handleDelete(entry)}
                        >
                          <Trash2 className="h-4 w-4" />
                        </Button>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="entries">
          <Card>
            <CardHeader>
              <CardTitle>All Food Entries</CardTitle>
            </CardHeader>
            <CardContent>
              {/* Food entries list would go here */}
              <div className="text-center py-8">
                <Calendar className="mx-auto h-12 w-12 text-gray-400" />
                <h3 className="mt-2 text-sm font-medium text-gray-900">Food Entries</h3>
                <p className="mt-1 text-sm text-gray-500">
                  View and manage all your food entries across different dates.
                </p>
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="analytics">
          <Card>
            <CardHeader>
              <CardTitle>Nutrition Analytics</CardTitle>
            </CardHeader>
            <CardContent>
              {/* Analytics charts would go here */}
              <div className="text-center py-8">
                <TrendingUp className="mx-auto h-12 w-12 text-gray-400" />
                <h3 className="mt-2 text-sm font-medium text-gray-900">Nutrition Analytics</h3>
                <p className="mt-1 text-sm text-gray-500">
                  Track your nutrition trends and patterns over time.
                </p>
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>

      {/* Add/Edit Form Modal */}
      {showAddForm && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
          <Card className="w-full max-w-md max-h-[90vh] overflow-y-auto">
            <CardHeader>
              <CardTitle>
                {editingEntry ? 'Edit Food Entry' : 'Add Food Entry'}
              </CardTitle>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSubmit} className="space-y-4">
                <div>
                  <Label htmlFor="meal">Meal</Label>                  <Select 
                    value={formData.meal} 
                    onValueChange={(value: 'breakfast' | 'lunch' | 'dinner' | 'snack') => setFormData({ ...formData, meal: value })}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Select meal" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="breakfast">Breakfast</SelectItem>
                      <SelectItem value="lunch">Lunch</SelectItem>
                      <SelectItem value="dinner">Dinner</SelectItem>
                      <SelectItem value="snack">Snack</SelectItem>
                    </SelectContent>
                  </Select>
                </div>

                <div>
                  <Label htmlFor="food">Food</Label>
                  <Input
                    id="food"
                    value={formData.food}
                    onChange={(e) => setFormData({ ...formData, food: e.target.value })}
                    placeholder="e.g., Grilled Chicken Breast"
                    required
                  />
                </div>

                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="calories">Calories</Label>
                    <Input
                      id="calories"
                      type="number"
                      value={formData.calories}
                      onChange={(e) => setFormData({ ...formData, calories: Number(e.target.value) })}
                      min="0"
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="protein">Protein (g)</Label>
                    <Input
                      id="protein"
                      type="number"
                      step="0.1"
                      value={formData.protein}
                      onChange={(e) => setFormData({ ...formData, protein: Number(e.target.value) })}
                      min="0"
                      required
                    />
                  </div>
                </div>

                <div className="grid grid-cols-3 gap-4">
                  <div>
                    <Label htmlFor="carbs">Carbs (g)</Label>
                    <Input
                      id="carbs"
                      type="number"
                      step="0.1"
                      value={formData.carbs}
                      onChange={(e) => setFormData({ ...formData, carbs: Number(e.target.value) })}
                      min="0"
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="fat">Fat (g)</Label>
                    <Input
                      id="fat"
                      type="number"
                      step="0.1"
                      value={formData.fat}
                      onChange={(e) => setFormData({ ...formData, fat: Number(e.target.value) })}
                      min="0"
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="fiber">Fiber (g)</Label>
                    <Input
                      id="fiber"
                      type="number"
                      step="0.1"
                      value={formData.fiber}
                      onChange={(e) => setFormData({ ...formData, fiber: Number(e.target.value) })}
                      min="0"
                    />
                  </div>
                </div>

                <div>
                  <Label htmlFor="notes">Notes (optional)</Label>
                  <Textarea
                    id="notes"
                    value={formData.notes}
                    onChange={(e) => setFormData({ ...formData, notes: e.target.value })}
                    placeholder="Any additional notes about this food..."
                    rows={3}
                  />
                </div>

                <div className="flex gap-2 pt-4">
                  <Button type="submit" disabled={isLoading} className="flex-1">
                    {isLoading ? 'Saving...' : editingEntry ? 'Update Entry' : 'Add Entry'}
                  </Button>
                  <Button type="button" variant="outline" onClick={resetForm}>
                    Cancel
                  </Button>
                </div>
              </form>
            </CardContent>
          </Card>
        </div>
      )}
    </div>
  );
};

export default NutritionPage;
