import React, { useMemo } from 'react';
import { useDashboardCustomization } from '@/store/dashboardCustomizationStore';
import { useWorkoutTemplates, useWorkoutLogs } from '@/hooks/useConsolidatedApp';
import { 
  TotalWorkoutsCard,
  WeeklyAverageCard,
  WeightLiftedCard,
  PersonalRecordsCard,
  ProgressionByMuscleCard,
  MuscleGroupVolumeCard,
  FatigueProgressionCard,
  NewWorkoutActionCard,
  AnalyticsActionCard,
  SetGoalsActionCard,
  WorkoutLogTableCard,
  WorkoutSummaryCard,
  ProgressCard,
  TodoCard
} from './cards';
import { MuscleFatigueCard } from './MuscleFatigueCard';
import { ActiveWorkoutCard } from './ActiveWorkoutCard';
import { RecentWorkouts } from './RecentWorkouts';
import { HealthDashboardWidget } from '@/components/health/HealthDashboardWidget';

export const CustomizableDashboardGrid: React.FC = () => {
  const { cards } = useDashboardCustomization();
  const { activeWorkout } = useWorkoutTemplates();
  const { workoutLogs } = useWorkoutLogs();

  // Component mapping
  const componentMap = useMemo(() => ({
    MuscleFatigueCard: () => <MuscleFatigueCard logs={workoutLogs} />,
    ActiveWorkoutCard: () => activeWorkout ? <ActiveWorkoutCard /> : null,
    TotalWorkoutsCard: () => <TotalWorkoutsCard />,
    WeeklyAverageCard: () => <WeeklyAverageCard />,
    WeightLiftedCard: () => <WeightLiftedCard />,
    PersonalRecordsCard: () => <PersonalRecordsCard />,
    ProgressCard: () => <ProgressCard />,
    HealthDashboardWidget: () => <HealthDashboardWidget />,
    TodoCard: () => <TodoCard />,
    NewWorkoutActionCard: () => <NewWorkoutActionCard />,
    AnalyticsActionCard: () => <AnalyticsActionCard />,
    SetGoalsActionCard: () => <SetGoalsActionCard />,
    ProgressionByMuscleCard: () => <ProgressionByMuscleCard />,
    MuscleGroupVolumeCard: () => <MuscleGroupVolumeCard />,
    FatigueProgressionCard: () => <FatigueProgressionCard />,
    WorkoutLogTableCard: () => <WorkoutLogTableCard />,
    WorkoutSummaryCard: () => <WorkoutSummaryCard />
  }), [workoutLogs, activeWorkout]);

  // Filter visible cards and organize by category
  const visibleCards = useMemo(() => {
    return cards.filter(card => {
      // Special case for ActiveWorkoutCard - only show if there's an active workout
      if (card.component === 'ActiveWorkoutCard' && !activeWorkout) {
        return false;
      }
      return card.isVisible;
    });
  }, [cards, activeWorkout]);

  // Group cards by category for better layout
  const cardsByCategory = useMemo(() => {
    const grouped = visibleCards.reduce((acc, card) => {
      if (!acc[card.category]) {
        acc[card.category] = [];
      }
      acc[card.category].push(card);
      return acc;
    }, {} as Record<string, typeof cards>);

    return grouped;
  }, [visibleCards]);

  const renderCard = (cardConfig: { id: string; component: string; name: string; isVisible: boolean; category: string }) => {
    const ComponentToRender = componentMap[cardConfig.component as keyof typeof componentMap];
    
    if (!ComponentToRender) {
      console.warn(`Component ${cardConfig.component} not found in componentMap`);
      return null;
    }

    const renderedComponent = ComponentToRender();
    
    // Skip rendering if component returns null
    if (!renderedComponent) {
      return null;
    }

    return (
      <div key={cardConfig.id} className="w-full">
        {renderedComponent}
      </div>
    );
  };

  const getCategoryGridClass = (category: string, cards: { id: string; component: string; name: string; isVisible: boolean; category: string }[]) => {
    // Check if this category contains the workout summary table - if so, give it special treatment
    const hasWorkoutSummary = cards.some(card => card.component === 'WorkoutSummaryCard');
    const hasWorkoutLogTable = cards.some(card => card.component === 'WorkoutLogTableCard');
    
    // Special case: When both tables are present, display them side by side
    if (hasWorkoutSummary && hasWorkoutLogTable) {
      return 'grid-cols-1 lg:grid-cols-2';
    }
    
    // Special case: Single table gets full width
    if (hasWorkoutSummary || hasWorkoutLogTable) {
      return 'grid-cols-1';
    }
    
    const cardCount = cards.length;
    
    switch (category) {
      case 'stat':
        // Stats cards in a 2x2 or 4x1 grid
        return cardCount <= 2 ? 'grid-cols-1 md:grid-cols-2' : 'grid-cols-2 md:grid-cols-4';
      case 'action':
        // Action cards in a row
        return 'grid-cols-1 md:grid-cols-3';
      case 'chart':
        // Charts get more space
        return 'grid-cols-1 lg:grid-cols-2';
      case 'content':
        // Content cards in flexible layout
        return cardCount === 1 ? 'grid-cols-1' : 'grid-cols-1 md:grid-cols-2';
      case 'health':
        // Health cards get full width initially
        return 'grid-cols-1';
      default:
        return 'grid-cols-1 md:grid-cols-2 lg:grid-cols-3';
    }
  };

  if (visibleCards.length === 0) {
    return (
      <div className="flex items-center justify-center h-64 text-muted-foreground">
        <div className="text-center">
          <p className="text-lg font-medium mb-2">No visible cards</p>
          <p className="text-sm">Use the "Manage Cards" button above to show cards</p>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-4">
      {Object.entries(cardsByCategory).map(([category, categoryCards]) => (
        <div key={category} className="space-y-3">
          <h3 className="text-sm font-medium text-muted-foreground uppercase tracking-wide">
            {category} ({categoryCards.length})
          </h3>
          
          <div className={`grid gap-4 ${getCategoryGridClass(category, categoryCards)} ${
            // Add equal height class when both tables are present
            categoryCards.some(card => card.component === 'WorkoutSummaryCard') && 
            categoryCards.some(card => card.component === 'WorkoutLogTableCard')
              ? 'grid-rows-1 min-h-[600px]'
              : ''
          }`}>
            {categoryCards.map(renderCard).filter(Boolean)}
          </div>
        </div>
      ))}
    </div>
  );
}; 