import React, { useMemo } from 'react';
import { StatCard } from '../StatCard';
import { BarChart2 } from 'lucide-react';
import { useWorkoutLogs } from '@/hooks/useConsolidatedApp';
import { getTotalWorkouts } from '@/utils/computeDailyMetrics';

export const TotalWorkoutsCard: React.FC = () => {
  const { workoutLogs } = useWorkoutLogs();
  const totalWorkouts = useMemo(() => getTotalWorkouts(workoutLogs), [workoutLogs]);

  return (
    <StatCard
      title="Total Workouts"
      value={totalWorkouts}
      description="Since you started"
      icon={<BarChart2 className="w-4 h-4 text-black" />}
      trend={{
        value: 0,
        isPositive: true
      }}
    />
  );
}; 