import React, { useState, useEffect, useMemo } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Plus, Brain, Clock, Sparkles, Calendar, Play, Pause, RotateCcw, Edit2, Trash2 } from 'lucide-react';
import { toast } from 'sonner';
import { useHealthStore } from '@/store/healthStore';
import { MeditationEntry as StoreMeditationEntry } from '@/types/Health';
import { format } from 'date-fns';
import { PageHeader } from '@/components/ui/PageHeader';

// Use store type directly
type MeditationEntry = StoreMeditationEntry;

interface MeditationSession {
  duration: number;
  type: string;
  isActive: boolean;
  startTime?: Date;
  timeLeft: number;
}

const MeditationPage: React.FC = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [showAddForm, setShowAddForm] = useState(false);
  const [editingEntry, setEditingEntry] = useState<MeditationEntry | null>(null);
  const [selectedDate, setSelectedDate] = useState(format(new Date(), 'yyyy-MM-dd'));
  const [activeTab, setActiveTab] = useState('today');

  // Timer state
  const [session, setSession] = useState<MeditationSession>({
    duration: 10,
    type: 'mindfulness',
    isActive: false,
    timeLeft: 10 * 60 // 10 minutes in seconds
  });
  const [formData, setFormData] = useState<MeditationEntry>({
    id: '',
    personId: 'self',
    date: selectedDate,
    type: 'mindfulness',
    duration: 0,
    moodBefore: 5,
    moodAfter: 5,
    stressBefore: 5,
    stressAfter: 5,
    guided: false,
    notes: '',
    timestamp: new Date().toISOString()
  });

  const meditationTypes = [
    { value: 'mindfulness', label: 'Mindfulness', description: 'Present moment awareness' },
    { value: 'breathing', label: 'Breathing', description: 'Focus on breath' },
    { value: 'body-scan', label: 'Body Scan', description: 'Progressive relaxation' },
    { value: 'loving-kindness', label: 'Loving Kindness', description: 'Compassion meditation' },
    { value: 'visualization', label: 'Visualization', description: 'Guided imagery' },
    { value: 'walking', label: 'Walking', description: 'Mindful movement' },
    { value: 'mantra', label: 'Mantra', description: 'Repetitive phrases' },
    { value: 'custom', label: 'Other', description: 'Custom practice' }
  ];
  const quickDurations = [5, 10, 15, 20, 30, 45, 60];

  // Use health store instead of direct API calls
  const { 
    meditationEntries, 
    isLoading: storeLoading, 
    isInitialized, 
    fetchAllHealthData,
    addMeditationEntry,
    updateMeditationEntryAction,
    deleteMeditationEntryAction
  } = useHealthStore();
  // Filter entries for selected date using memoized computed value
  const entries = useMemo(() => 
    meditationEntries.filter(entry => entry.date === selectedDate),
    [meditationEntries, selectedDate]
  );

  useEffect(() => {
    if (!isInitialized) {
      fetchAllHealthData();
    }
  }, [isInitialized, fetchAllHealthData]);

  // Update local loading state based on store loading
  useEffect(() => {
    setIsLoading(storeLoading);
  }, [storeLoading]);

  // Timer effect
  useEffect(() => {
    let interval: NodeJS.Timeout;
    
    if (session.isActive && session.timeLeft > 0) {
      interval = setInterval(() => {
        setSession(prev => ({
          ...prev,
          timeLeft: prev.timeLeft - 1
        }));
      }, 1000);
    } else if (session.timeLeft === 0 && session.isActive) {
      // Session completed
      setSession(prev => ({ ...prev, isActive: false }));
      toast.success(`Great job! You meditated for ${session.duration} minutes.`, {
        description: "Meditation Complete! "
      });
    }

    return () => clearInterval(interval);
  }, [session.isActive, session.timeLeft, session.duration]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);

    try {
      if (editingEntry?.id) {
        await updateMeditationEntryAction(editingEntry.id, formData);
        toast.success("Meditation entry updated successfully!");
      } else {
        await addMeditationEntry(formData);
        toast.success("Meditation entry added successfully!");
      }
      resetForm();
      // Data will be automatically updated in the store
    } catch (error) {
      console.error('Error saving meditation entry:', error);
      toast.error("Failed to save meditation entry. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  const handleEdit = (entry: MeditationEntry) => {
    setEditingEntry(entry);
    setFormData(entry);
    setShowAddForm(true);
  };
  const handleDelete = async (entry: MeditationEntry) => {
    if (!confirm('Are you sure you want to delete this entry?')) return;

    setIsLoading(true);
    try {
      if (entry.id) {
        await deleteMeditationEntryAction(entry.id);
        toast.success("Meditation entry deleted successfully!");
        // Data will be automatically updated in the store
      }
    } catch (error) {
      console.error('Error deleting meditation entry:', error);
      toast.error("Failed to delete meditation entry. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };
  const resetForm = () => {
    setFormData({
      id: '',
      personId: 'self',
      date: selectedDate,
      type: 'mindfulness',
      duration: 0,
      moodBefore: 5,
      moodAfter: 5,
      stressBefore: 5,
      stressAfter: 5,
      guided: false,
      notes: '',
      timestamp: new Date().toISOString()
    });
    setEditingEntry(null);
    setShowAddForm(false);
  };

  const startTimer = () => {
    setSession(prev => ({
      ...prev,
      isActive: true,
      startTime: new Date(),
      timeLeft: prev.duration * 60
    }));
  };

  const pauseTimer = () => {
    setSession(prev => ({ ...prev, isActive: false }));
  };

  const resetTimer = () => {
    setSession(prev => ({
      ...prev,
      isActive: false,
      timeLeft: prev.duration * 60
    }));
  };

  const formatTime = (seconds: number) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
  };

  const getMoodEmoji = (mood: number) => {
    if (mood <= 2) return '';
    if (mood <= 4) return '';
    if (mood <= 6) return '';
    if (mood <= 8) return '';
    return '';
  };

  const getStressColor = (stress: number) => {
    if (stress <= 3) return 'text-green-600';
    if (stress <= 6) return 'text-yellow-600';
    return 'text-red-600';
  };

  const calculateTotalTime = () => {
    return entries.reduce((total, entry) => total + entry.duration, 0);
  };
  const calculateAverages = () => {
    if (entries.length === 0) return { mood: 0, stress: 0 };
    return {
      mood: entries.reduce((sum, entry) => sum + entry.moodAfter, 0) / entries.length,
      stress: entries.reduce((sum, entry) => sum + entry.stressAfter, 0) / entries.length
    };
  };

  const todayStats = calculateAverages();

  return (
    <div className="p-2 sm:p-4 md:p-6 space-y-4 md:space-y-6 min-h-[80vh]">
      <PageHeader
        icon={Brain}
        title="Meditation & Mindfulness"
        date={selectedDate}
        onDateChange={setSelectedDate}
        action={
          <Button onClick={() => setShowAddForm(true)} aria-label="Log Session">
            <Plus className="h-4 w-4" /> Log Session
          </Button>
        }
      />

      <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
        <TabsList className="grid w-full grid-cols-4">
          <TabsTrigger value="today">Today</TabsTrigger>
          <TabsTrigger value="timer">Meditation Timer</TabsTrigger>
          <TabsTrigger value="history">History</TabsTrigger>
          <TabsTrigger value="insights">Insights</TabsTrigger>
        </TabsList>

        <TabsContent value="today" className="space-y-6">
          {/* Today's Stats */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <Card>
              <CardContent className="p-4">
                <div className="flex items-center gap-2">
                  <Clock className="h-4 w-4 text-blue-500" />
                  <span className="font-medium text-sm">Total Time</span>
                </div>
                <div className="mt-2">
                  <span className="text-2xl font-bold">{calculateTotalTime()}</span>
                  <span className="text-sm text-gray-500 ml-1">minutes</span>
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardContent className="p-4">
                <div className="flex items-center gap-2">
                  <Brain className="h-4 w-4 text-green-500" />
                  <span className="font-medium text-sm">Sessions</span>
                </div>
                <div className="mt-2">
                  <span className="text-2xl font-bold">{entries.length}</span>
                  <span className="text-sm text-gray-500 ml-1">today</span>
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardContent className="p-4">
                <div className="flex items-center gap-2">
                  <Sparkles className="h-4 w-4 text-purple-500" />
                  <span className="font-medium text-sm">Avg. Mood</span>
                </div>
                <div className="mt-2 flex items-center gap-2">
                  <span className="text-2xl font-bold">{todayStats.mood.toFixed(1)}</span>
                  <span className="text-lg">{getMoodEmoji(todayStats.mood)}</span>
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Today's Sessions */}
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Calendar className="h-5 w-5" />
                Today's Meditation Sessions
              </CardTitle>
            </CardHeader>
            <CardContent>
              {entries.length === 0 ? (
                <div className="text-center py-8">
                  <Brain className="mx-auto h-12 w-12 text-gray-400" />
                  <h3 className="mt-2 text-sm font-medium text-gray-900">No sessions today</h3>
                  <p className="mt-1 text-sm text-gray-500">
                    Start your mindfulness journey by logging your first meditation session.
                  </p>
                </div>
              ) : (
                <div className="space-y-4">
                  {entries.map((entry, index) => (
                    <div key={index} className="flex items-center justify-between p-4 border border-gray-200 rounded-lg">
                      <div className="flex-1">
                        <div className="flex items-center gap-3">
                          <Badge variant="outline" className="bg-purple-100 text-purple-800">
                            {meditationTypes.find(t => t.value === entry.type)?.label}
                          </Badge>
                          <span className="font-medium">{entry.duration} minutes</span>
                          {entry.guided && (
                            <Badge variant="secondary" className="text-xs">
                              Guided
                            </Badge>
                          )}
                        </div>
                        <div className="mt-2 text-sm text-gray-600 grid grid-cols-2 md:grid-cols-3 gap-4">
                          <span>Mood: {getMoodEmoji(entry.moodBefore)}  {getMoodEmoji(entry.moodAfter)}</span>
                          <span className={getStressColor(entry.stressAfter)}>
                            Stress: {entry.stressBefore}  {entry.stressAfter}
                          </span>
                          <span>{entry.timestamp && format(new Date(entry.timestamp), 'h:mm a')}</span>
                        </div>
                        {entry.notes && (
                          <p className="mt-1 text-sm text-gray-500">{entry.notes}</p>
                        )}
                      </div>
                      <div className="flex items-center gap-2">
                        <Button
                          variant="outline"
                          size="sm"
                          onClick={() => handleEdit(entry)}
                        >
                          <Edit2 className="h-4 w-4" />
                        </Button>
                        <Button
                          variant="outline"
                          size="sm"
                          onClick={() => handleDelete(entry)}
                        >
                          <Trash2 className="h-4 w-4" />
                        </Button>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="timer">
          <Card className="max-w-md mx-auto">
            <CardHeader className="text-center">
              <CardTitle>Meditation Timer</CardTitle>
            </CardHeader>
            <CardContent className="space-y-6">
              {/* Timer Display */}
              <div className="text-center">
                <div className="text-6xl font-bold text-purple-600 mb-4">
                  {formatTime(session.timeLeft)}
                </div>
                <div className="text-lg text-gray-600 mb-6">
                  {meditationTypes.find(t => t.value === session.type)?.label} Meditation
                </div>
              </div>

              {/* Duration Selection */}
              {!session.isActive && (
                <div className="space-y-4">
                  <div>
                    <Label>Duration (minutes)</Label>
                    <div className="grid grid-cols-4 gap-2 mt-2">
                      {quickDurations.map((duration) => (
                        <Button
                          key={duration}
                          variant={session.duration === duration ? "default" : "outline"}
                          size="sm"
                          onClick={() => setSession(prev => ({ 
                            ...prev, 
                            duration, 
                            timeLeft: duration * 60 
                          }))}
                        >
                          {duration}m
                        </Button>
                      ))}
                    </div>
                  </div>

                  <div>
                    <Label>Meditation Type</Label>
                    <Select 
                      value={session.type} 
                      onValueChange={(value) => setSession(prev => ({ ...prev, type: value }))}
                    >
                      <SelectTrigger>
                        <SelectValue />
                      </SelectTrigger>
                      <SelectContent>
                        {meditationTypes.map((type) => (
                          <SelectItem key={type.value} value={type.value}>
                            {type.label}
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>
                </div>
              )}

              {/* Timer Controls */}
              <div className="flex justify-center gap-4">
                {!session.isActive ? (
                  <Button onClick={startTimer} size="lg" className="flex items-center gap-2">
                    <Play className="h-5 w-5" />
                    Start
                  </Button>
                ) : (
                  <Button onClick={pauseTimer} size="lg" variant="outline" className="flex items-center gap-2">
                    <Pause className="h-5 w-5" />
                    Pause
                  </Button>
                )}
                <Button onClick={resetTimer} size="lg" variant="outline" className="flex items-center gap-2">
                  <RotateCcw className="h-5 w-5" />
                  Reset
                </Button>
              </div>

              {/* Quick Log Button */}
              {session.timeLeft === 0 && (
                <Button
                  onClick={() => {
                    setFormData({
                      ...formData,
                      type: session.type as MeditationEntry['type'],
                      duration: session.duration
                    });
                    setShowAddForm(true);
                  }}
                  className="w-full"
                >
                  Log This Session
                </Button>
              )}
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="history">
          <Card>
            <CardHeader>
              <CardTitle>Meditation History</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-center py-8">
                <Calendar className="mx-auto h-12 w-12 text-gray-400" />
                <h3 className="mt-2 text-sm font-medium text-gray-900">Meditation History</h3>
                <p className="mt-1 text-sm text-gray-500">
                  View your meditation practice over time.
                </p>
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="insights">
          <Card>
            <CardHeader>
              <CardTitle>Meditation Insights</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-center py-8">
                <Sparkles className="mx-auto h-12 w-12 text-gray-400" />
                <h3 className="mt-2 text-sm font-medium text-gray-900">Meditation Insights</h3>
                <p className="mt-1 text-sm text-gray-500">
                  Discover patterns and insights from your meditation practice.
                </p>
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>

      {/* Add/Edit Form Modal */}
      {showAddForm && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
          <Card className="w-full max-w-2xl max-h-[90vh] overflow-y-auto">
            <CardHeader>
              <CardTitle>
                {editingEntry ? 'Edit Meditation Session' : 'Log Meditation Session'}
              </CardTitle>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSubmit} className="space-y-4">
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="type">Meditation Type</Label>
                    <Select 
                      value={formData.type} 
                      onValueChange={(value: MeditationEntry['type']) => setFormData({ ...formData, type: value })}
                    >
                      <SelectTrigger>
                        <SelectValue />
                      </SelectTrigger>
                      <SelectContent>
                        {meditationTypes.map((type) => (
                          <SelectItem key={type.value} value={type.value}>
                            <div>
                              <div>{type.label}</div>
                              <div className="text-xs text-gray-500">{type.description}</div>
                            </div>
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>

                  <div>
                    <Label htmlFor="duration">Duration (minutes)</Label>
                    <Input
                      id="duration"
                      type="number"
                      value={formData.duration}
                      onChange={(e) => setFormData({ ...formData, duration: Number(e.target.value) })}
                      min="1"
                      required
                    />
                  </div>
                </div>
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="moodBefore">Mood Before (1-10)</Label>
                    <Input
                      id="moodBefore"
                      type="number"
                      value={formData.moodBefore}
                      onChange={(e) => setFormData({ ...formData, moodBefore: Number(e.target.value) })}
                      min="1"
                      max="10"
                      required
                    />
                  </div>

                  <div>
                    <Label htmlFor="moodAfter">Mood After (1-10)</Label>
                    <Input
                      id="moodAfter"
                      type="number"
                      value={formData.moodAfter}
                      onChange={(e) => setFormData({ ...formData, moodAfter: Number(e.target.value) })}
                      min="1"
                      max="10"
                      required
                    />
                  </div>
                </div>

                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="stressBefore">Stress Before (1-10)</Label>
                    <Input
                      id="stressBefore"
                      type="number"
                      value={formData.stressBefore}
                      onChange={(e) => setFormData({ ...formData, stressBefore: Number(e.target.value) })}
                      min="1"
                      max="10"
                      required
                    />
                  </div>

                  <div>
                    <Label htmlFor="stressAfter">Stress After (1-10)</Label>
                    <Input
                      id="stressAfter"
                      type="number"
                      value={formData.stressAfter}
                      onChange={(e) => setFormData({ ...formData, stressAfter: Number(e.target.value) })}
                      min="1"
                      max="10"
                      required
                    />
                  </div>
                </div>

                <div className="flex items-center space-x-2">
                  <input
                    type="checkbox"
                    id="guided"
                    checked={formData.guided}
                    onChange={(e) => setFormData({ ...formData, guided: e.target.checked })}
                    className="rounded"
                  />
                  <Label htmlFor="guided">Guided meditation</Label>
                </div>

                <div>
                  <Label htmlFor="notes">Notes (optional)</Label>
                  <Textarea
                    id="notes"
                    value={formData.notes}
                    onChange={(e) => setFormData({ ...formData, notes: e.target.value })}
                    placeholder="How did the session go? Any insights or observations..."
                    rows={3}
                  />
                </div>

                <div className="flex gap-2 pt-4">
                  <Button type="submit" disabled={isLoading} className="flex-1">
                    {isLoading ? 'Saving...' : editingEntry ? 'Update Session' : 'Log Session'}
                  </Button>
                  <Button type="button" variant="outline" onClick={resetForm}>
                    Cancel
                  </Button>
                </div>
              </form>
            </CardContent>
          </Card>
        </div>
      )}
    </div>
  );
};

export default MeditationPage;
