import React from 'react';
import { Button } from '@/components/ui/button';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import { Badge } from '@/components/ui/badge';
import { useHealthStore } from '@/store/healthStore';
import { Plus, Users } from 'lucide-react';

// Simple Avatar component replacement
const Avatar: React.FC<{ children: React.ReactNode; className?: string }> = ({ children, className = '' }) => (
  <div className={`flex items-center justify-center rounded-full bg-gray-200 ${className}`}>
    {children}
  </div>
);

const AvatarFallback: React.FC<{ children: React.ReactNode; className?: string }> = ({ children, className = '' }) => (
  <span className={`text-sm font-medium text-gray-600 ${className}`}>{children}</span>
);

const AvatarImage: React.FC<{ src?: string | undefined; alt?: string }> = ({ src, alt }) => {
  if (!src) return null;
  return <img src={src} alt={alt} className="h-full w-full rounded-full object-cover" />;
};

interface PersonSelectorProps {
  onAddPerson?: () => void;
  showAddButton?: boolean;
  className?: string;
}

export const PersonSelector: React.FC<PersonSelectorProps> = ({
  onAddPerson,
  showAddButton = true,
  className = ''
}) => {
  const { people, currentPersonId, setCurrentPersonId } = useHealthStore();
  const currentPerson = people.find(p => p.id === currentPersonId) || {
    id: 'self',
    name: 'Self',
    relationship: 'self' as const,
    avatar: undefined
  };

  const getPersonInitials = (name: string) => {
    return name.split(' ').map(n => n[0]).join('').toUpperCase().slice(0, 2);
  };

  const getRelationshipColor = (relationship: string) => {
    switch (relationship) {
      case 'self': return 'bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-300';
      case 'family': return 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-300';
      case 'friend': return 'bg-purple-100 text-purple-800 dark:bg-purple-900 dark:text-purple-300';
      default: return 'bg-gray-100 text-gray-800 dark:bg-gray-900 dark:text-gray-300';
    }
  };

  return (
    <div className={`flex items-center gap-3 ${className}`}>
      <div className="flex items-center gap-2">
        <Users className="h-4 w-4 text-muted-foreground" />
        <span className="text-sm font-medium text-muted-foreground">Tracking for:</span>
      </div>
      
      <Select value={currentPersonId} onValueChange={setCurrentPersonId}>
        <SelectTrigger className="w-48">
          <SelectValue>
            <div className="flex items-center gap-2">
              <Avatar className="h-6 w-6">
                <AvatarImage src={currentPerson.avatar} />
                <AvatarFallback className="text-xs">
                  {getPersonInitials(currentPerson.name)}
                </AvatarFallback>
              </Avatar>
              <span>{currentPerson.name}</span>
              <Badge 
                variant="secondary" 
                className={`text-xs ${getRelationshipColor(currentPerson.relationship)}`}
              >
                {currentPerson.relationship}
              </Badge>
            </div>
          </SelectValue>
        </SelectTrigger>
        <SelectContent>
          {/* Default Self option if not in people array */}
          {!people.find(p => p.id === 'self') && (
            <SelectItem value="self">
              <div className="flex items-center gap-2">
                <Avatar className="h-6 w-6">
                  <AvatarFallback className="text-xs">SE</AvatarFallback>
                </Avatar>
                <span>Self</span>
                <Badge variant="secondary" className="text-xs bg-blue-100 text-blue-800">
                  self
                </Badge>
              </div>
            </SelectItem>
          )}
            {people
            .filter(person => person.id && person.id.trim() !== '')
            .map((person) => (
            <SelectItem key={person.id} value={person.id}>
              <div className="flex items-center gap-2">
                <Avatar className="h-6 w-6">
                  <AvatarImage src={person.avatar} />
                  <AvatarFallback className="text-xs">
                    {getPersonInitials(person.name)}
                  </AvatarFallback>
                </Avatar>
                <span>{person.name}</span>
                <Badge 
                  variant="secondary" 
                  className={`text-xs ${getRelationshipColor(person.relationship)}`}
                >
                  {person.relationship}
                </Badge>
              </div>
            </SelectItem>
          ))}
        </SelectContent>
      </Select>

      {showAddButton && (
        <Button
          variant="outline"
          size="sm"
          onClick={onAddPerson}
          className="flex items-center gap-1"
        >
          <Plus className="h-4 w-4" />
          Add Person
        </Button>
      )}
    </div>
  );
};
