import type { NutritionEntry, HealthEntry, MeditationEntry, NutritionGoals, HealthSummary } from '@/types/Health';

// Date utilities
export const formatDate = (date: Date | string): string => {
  if (typeof date === 'string') {
    date = new Date(date);
  }
  return date.toISOString().split('T')[0];
};

export const getWeekDates = (date: Date = new Date()): string[] => {
  const week = [];
  const startOfWeek = new Date(date);
  startOfWeek.setDate(date.getDate() - date.getDay());
  
  for (let i = 0; i < 7; i++) {
    const currentDate = new Date(startOfWeek);
    currentDate.setDate(startOfWeek.getDate() + i);
    week.push(formatDate(currentDate));
  }
  
  return week;
};

// Nutrition calculations
export const calculateDailyNutrition = (entries: NutritionEntry[], date: string) => {
  const dayEntries = entries.filter(entry => entry.date === date);
  
  return dayEntries.reduce(
    (totals, entry) => ({
      calories: totals.calories + entry.calories,
      protein: totals.protein + entry.protein,
      carbs: totals.carbs + entry.carbs,
      fat: totals.fat + entry.fat,
      fiber: totals.fiber + entry.fiber,
    }),
    { calories: 0, protein: 0, carbs: 0, fat: 0, fiber: 0 }
  );
};

export const calculateNutritionProgress = (
  current: ReturnType<typeof calculateDailyNutrition>,
  goals: NutritionGoals
) => ({
  calories: goals.dailyCalories > 0 ? (current.calories / goals.dailyCalories) * 100 : 0,
  protein: goals.protein > 0 ? (current.protein / goals.protein) * 100 : 0,
  carbs: goals.carbs > 0 ? (current.carbs / goals.carbs) * 100 : 0,
  fat: goals.fat > 0 ? (current.fat / goals.fat) * 100 : 0,
  fiber: goals.fiber > 0 ? (current.fiber / goals.fiber) * 100 : 0,
});

// Health metrics calculations
export const getLatestHealthMetric = (entries: HealthEntry[], type: string): HealthEntry | null => {
  const typeEntries = entries.filter(entry => entry.type === type);
  if (typeEntries.length === 0) return null;
  
  return typeEntries.reduce((latest, current) => 
    new Date(current.timestamp) > new Date(latest.timestamp) ? current : latest
  );
};

export const calculateHealthTrend = (entries: HealthEntry[], type: string, days: number = 7): number => {
  const typeEntries = entries
    .filter(entry => entry.type === type)
    .sort((a, b) => new Date(b.timestamp).getTime() - new Date(a.timestamp).getTime())
    .slice(0, days);
    
  if (typeEntries.length < 2) return 0;
  
  const latest = typeEntries[0].value;
  const oldest = typeEntries[typeEntries.length - 1].value;
  
  return ((latest - oldest) / oldest) * 100;
};

// Meditation calculations
export const calculateMeditationStats = (entries: MeditationEntry[], days: number = 7) => {
  const cutoffDate = new Date();
  cutoffDate.setDate(cutoffDate.getDate() - days);
  
  const recentEntries = entries.filter(entry => 
    new Date(entry.timestamp) >= cutoffDate
  );
  
  if (recentEntries.length === 0) {
    return {
      totalSessions: 0,
      totalMinutes: 0,
      averageDuration: 0,
      averageMoodImprovement: 0,
      averageStressReduction: 0,
      streak: 0
    };
  }
  
  const totalMinutes = recentEntries.reduce((sum, entry) => sum + entry.duration, 0);
  const moodImprovements = recentEntries.map(entry => entry.moodAfter - entry.moodBefore);
  const stressReductions = recentEntries.map(entry => entry.stressBefore - entry.stressAfter);
  
  return {
    totalSessions: recentEntries.length,
    totalMinutes,
    averageDuration: totalMinutes / recentEntries.length,
    averageMoodImprovement: moodImprovements.reduce((sum, val) => sum + val, 0) / moodImprovements.length,
    averageStressReduction: stressReductions.reduce((sum, val) => sum + val, 0) / stressReductions.length,
    streak: calculateMeditationStreak(entries)
  };
};

export const calculateMeditationStreak = (entries: MeditationEntry[]): number => {
  if (entries.length === 0) return 0;
  
  const sortedEntries = entries
    .sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime())
    .map(entry => entry.date);
    
  const uniqueDates = [...new Set(sortedEntries)];
  
  let streak = 0;
  const today = formatDate(new Date());
  let checkDate = today;
  
  for (const date of uniqueDates) {
    if (date === checkDate) {
      streak++;
      const prevDate = new Date(checkDate);
      prevDate.setDate(prevDate.getDate() - 1);
      checkDate = formatDate(prevDate);
    } else {
      break;
    }
  }
  
  return streak;
};

// Weekly health summary
export const generateWeeklyHealthSummary = (
  nutritionEntries: NutritionEntry[],
  healthEntries: HealthEntry[],
  meditationEntries: MeditationEntry[],
  weekDates: string[]
): HealthSummary[] => {
  return weekDates.map(date => {
    const nutrition = calculateDailyNutrition(nutritionEntries, date);
    const dayHealthEntries = healthEntries.filter(entry => entry.date === date);
    const dayMeditationEntries = meditationEntries.filter(entry => entry.date === date);
    
    const getHealthValue = (type: string) => {
      const entry = dayHealthEntries.find(e => e.type === type);
      return entry ? entry.value : undefined;
    };
    
    const totalMeditationMinutes = dayMeditationEntries.reduce(
      (sum, entry) => sum + entry.duration, 0
    );
    
    return {
      date,
      weight: getHealthValue('weight'),
      bodyFat: getHealthValue('body_fat'),
      muscleMass: getHealthValue('muscle_mass'),
      waterIntake: getHealthValue('water_intake'),
      steps: getHealthValue('steps'),
      sleepHours: getHealthValue('sleep_hours'),
      meditationMinutes: totalMeditationMinutes || undefined,
      calories: nutrition.calories || undefined,
      protein: nutrition.protein || undefined,
      carbs: nutrition.carbs || undefined,
      fat: nutrition.fat || undefined,
    };
  });
};

// Error handling utilities
export const handleHealthApiError = (error: any): string => {
  if (error?.message) {
    return error.message;
  }
  
  if (typeof error === 'string') {
    return error;
  }
  
  return 'An unexpected error occurred. Please try again.';
};

// Validation utilities
export const validateNutritionEntry = (entry: Partial<NutritionEntry>): string[] => {
  const errors: string[] = [];
  
  if (!entry.date) errors.push('Date is required');
  if (!entry.meal) errors.push('Meal type is required');
  if (!entry.food) errors.push('Food name is required');
  if (!entry.calories || entry.calories < 0) errors.push('Valid calories amount is required');
  if (entry.protein && entry.protein < 0) errors.push('Protein cannot be negative');
  if (entry.carbs && entry.carbs < 0) errors.push('Carbs cannot be negative');
  if (entry.fat && entry.fat < 0) errors.push('Fat cannot be negative');
  if (entry.fiber && entry.fiber < 0) errors.push('Fiber cannot be negative');
  
  return errors;
};

export const validateHealthEntry = (entry: Partial<HealthEntry>): string[] => {
  const errors: string[] = [];
  
  if (!entry.date) errors.push('Date is required');
  if (!entry.type) errors.push('Health metric type is required');
  if (!entry.value || entry.value < 0) errors.push('Valid value is required');
  
  return errors;
};

export const validateMeditationEntry = (entry: Partial<MeditationEntry>): string[] => {
  const errors: string[] = [];
  
  if (!entry.date) errors.push('Date is required');
  if (!entry.type) errors.push('Meditation type is required');
  if (!entry.duration || entry.duration <= 0) errors.push('Valid duration is required');
  if (entry.moodBefore !== undefined && (entry.moodBefore < 1 || entry.moodBefore > 10)) {
    errors.push('Mood before must be between 1-10');
  }
  if (entry.moodAfter !== undefined && (entry.moodAfter < 1 || entry.moodAfter > 10)) {
    errors.push('Mood after must be between 1-10');
  }
  if (entry.stressBefore !== undefined && (entry.stressBefore < 1 || entry.stressBefore > 10)) {
    errors.push('Stress before must be between 1-10');
  }
  if (entry.stressAfter !== undefined && (entry.stressAfter < 1 || entry.stressAfter > 10)) {
    errors.push('Stress after must be between 1-10');
  }
  
  return errors;
};
