// Workout Charts Dashboard - Main component for displaying all charts
import React from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { WorkoutLogEntry } from '@/types/Workout_Log';
import {
  MuscleGroupVolumeDistributionRadarChart,
  ProgressionByMuscleChart
} from './index';
import { FatigueProgressionByDateChart } from './FatigueProgressionByMuscleChart';

interface WorkoutChartsDashboardProps {
  workoutLogs: WorkoutLogEntry[];
  isLoading?: boolean;
  error?: string | null;
}

export const WorkoutChartsDashboard: React.FC<WorkoutChartsDashboardProps> = ({
  workoutLogs,
  isLoading = false,
  error
}) => {
  // Loading state
  if (isLoading) {
    return (
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {[...Array(3)].map((_, i) => (
          <Card key={i} className="h-[400px]">
            <CardHeader>
              <CardTitle className="h-4 bg-gray-200 rounded animate-pulse" />
            </CardHeader>
            <CardContent>
              <div className="h-[300px] bg-gray-100 rounded animate-pulse" />
            </CardContent>
          </Card>
        ))}
      </div>
    );
  }

  // Error state
  if (error) {
    return (
      <Card className="p-6">
        <div className="text-center text-red-600">
          <p>Error loading charts: {error}</p>
        </div>
      </Card>
    );
  }

  // No data state
  if (!workoutLogs || workoutLogs.length === 0) {
    return (
      <div className="space-y-6">
        <div className="p-8 text-center border-2 border-dashed border-gray-300 dark:border-gray-600 rounded-lg">
          <h3 className="text-gray-600 dark:text-gray-400 font-medium">No Workout Data</h3>
          <p className="text-gray-500 dark:text-gray-500 text-sm mt-1">
            Start logging workouts to see your progress charts
          </p>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div className="text-center mb-6">
        <h2 className="text-2xl font-bold">Workout Analytics Dashboard</h2>
        <p className="text-muted-foreground">Track your progression, weight distribution, and fatigue levels over time</p>
      </div>
      
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <div>
          <ProgressionByMuscleChart logs={workoutLogs} />
        </div>
        
        <div>
          <MuscleGroupVolumeDistributionRadarChart logs={workoutLogs} />
        </div>
        
        <div className="lg:col-span-2">
          <FatigueProgressionByDateChart logs={workoutLogs} />
        </div>
      </div>
    </div>
  );
};

// Export as default for easy importing
export default WorkoutChartsDashboard; 