import React from 'react';
import { FatigueProgressionByDateChart } from '@/components/charts/FatigueProgressionByMuscleChart';
import { useWorkoutLogs } from '@/hooks/useConsolidatedApp';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';

export const FatigueProgressionCard: React.FC = () => {
  const { workoutLogs, isLoading, error } = useWorkoutLogs();

  if (isLoading) {
    return (
      <Card className="h-[400px]">
        <CardHeader>
          <CardTitle className="h-4 bg-gray-200 rounded animate-pulse" />
        </CardHeader>
        <CardContent>
          <div className="h-[300px] bg-gray-100 rounded animate-pulse" />
        </CardContent>
      </Card>
    );
  }

  if (error) {
    return (
      <Card className="p-6">
        <div className="text-center text-red-600">
          <p>Error loading fatigue chart: {error}</p>
        </div>
      </Card>
    );
  }

  if (!workoutLogs || workoutLogs.length === 0) {
    return (
      <Card className="p-8">
        <div className="text-center border-2 border-dashed border-gray-300 dark:border-gray-600 rounded-lg p-6">
          <h3 className="text-gray-600 dark:text-gray-400 font-medium">No Fatigue Data</h3>
          <p className="text-gray-500 dark:text-gray-500 text-sm mt-1">
            Start logging workouts to see fatigue progression over time
          </p>
        </div>
      </Card>
    );
  }

  return <FatigueProgressionByDateChart logs={workoutLogs} />;
}; 