import React, { useMemo } from 'react';
import { StatCard } from '../StatCard';
import { TrendingUp } from 'lucide-react';
import { useWorkoutLogs } from '@/hooks/useConsolidatedApp';
import { getWeeklyAverageWorkouts } from '@/utils/computeDailyMetrics';

export const WeeklyAverageCard: React.FC = () => {
  const { workoutLogs } = useWorkoutLogs();
  const weeklyAverage = useMemo(() => getWeeklyAverageWorkouts(workoutLogs), [workoutLogs]);

  return (
    <StatCard
      title="Weekly Average"
      value={weeklyAverage.toString()}
      description="Sessions per week"
      icon={<TrendingUp className="w-4 h-4 text-black" />}
      trend={{
        value: 0,
        isPositive: true
      }}
    />
  );
}; 