import React from 'react';
import { useNavigation } from '@/hooks/useConsolidatedApp';
import { DashboardMainContent } from './DashboardMainContent';
import { WorkoutsContent } from './WorkoutsContent';
import { ScheduleContent } from './ScheduleContent';
import { StatsContent } from './StatsContent';
import { ProfileContent } from './ProfileContent';
import { SettingsContent } from './SettingsContent';
import MuscleFatiguePage from '@/views/MuscleFatiguePage';
import SleepTrackingPage from '@/views/SleepTrackingPage';
import NutritionPage from '@/views/NutritionPage';
import MealPlanningPage from '@/views/MealPlanningPage';
import HealthTrackingPage from '@/views/HealthTrackingPage';
import MeditationPage from '@/views/MeditationPage';
import TodosPage from '@/views/TodosPage';

export const ViewContent: React.FC = () => {
  const { currentView } = useNavigation();  // Return different content based on currentView
  switch (currentView) {
    case 'dashboard':
      return <DashboardMainContent />;
    case 'workouts':
      return <WorkoutsContent />;
    case 'schedule':
      return <ScheduleContent />;
    case 'stats':
      return <StatsContent />;
    case 'profile':
      return <ProfileContent />;
    case 'settings':
      return <SettingsContent />;
    case 'todos':
      return <TodosPage />;
    case 'muscleFatigue':
      return <MuscleFatiguePage />;
    case 'sleep-tracking':
      return <SleepTrackingPage />;
    case 'nutrition':
      return <NutritionPage />;
    case 'meal-planning':
      return <MealPlanningPage />;
    case 'health-tracking':
      return <HealthTrackingPage />;
    case 'meditation':
      return <MeditationPage />;
    default:
      return <DashboardMainContent />;
  }
};
