import React from 'react';
import { Card, CardContent, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { cn } from '@/lib/utils';
import { ArrowUp, ArrowDown } from 'lucide-react';

interface StatCardProps {
  title: string;
  value: string | number;
  description?: string;
  icon?: React.ReactNode;
  trend?: {
    value: number;
    isPositive: boolean;
  };
  className?: string;
}

export const StatCard = ({
  title,
  value,
  description,
  icon,
  trend,
  className,
}: StatCardProps) => {
  // Responsive styles based on CSS variables from DraggableCard
  const responsiveStyles = {
    // Use CSS custom properties for responsive sizing
    fontSize: 'clamp(0.75rem, calc(var(--grid-width, 3) * 0.25rem), 1rem)',
    iconSize: 'clamp(1rem, calc(var(--grid-width, 3) * 0.33rem), 2rem)',
    padding: 'clamp(0.5rem, calc(var(--grid-width, 3) * 0.16rem), 1.5rem)',
    valueSize: 'clamp(1.25rem, calc(var(--grid-width, 3) * 0.5rem), 2.5rem)',
  } as React.CSSProperties;

  return (
    <Card 
      variant="glass"
      hover="lift"
      className={cn("overflow-hidden h-full", className)}
      style={responsiveStyles}
    >
      <CardHeader className={cn(
        "flex flex-row items-center justify-between space-y-0",
        "pb-[calc(var(--grid-height,3)*0.15rem)]",
        "px-[calc(var(--grid-width,3)*0.16rem)]",
        "pt-[calc(var(--grid-height,3)*0.2rem)]"
      )}>
        <CardTitle 
          className="font-medium text-muted-foreground line-clamp-1"
          style={{ 
            fontSize: 'clamp(0.75rem, calc(var(--grid-width, 3) * 0.25rem), 0.9rem)' 
          }}
        >
          {title}
        </CardTitle>
        {icon && (
          <div 
            className="flex items-center justify-center rounded-full bg-primary text-white shadow-sm shrink-0"
            style={{
              width: 'clamp(1.5rem, calc(var(--grid-width, 3) * 0.5rem), 2.5rem)',
              height: 'clamp(1.5rem, calc(var(--grid-width, 3) * 0.5rem), 2.5rem)',
            }}
          >
            <div style={{ 
              width: 'clamp(0.75rem, calc(var(--grid-width, 3) * 0.25rem), 1.25rem)',
              height: 'clamp(0.75rem, calc(var(--grid-width, 3) * 0.25rem), 1.25rem)'
            }}>
              {icon}
            </div>
          </div>
        )}
      </CardHeader>
      <CardContent className={cn(
        "px-[calc(var(--grid-width,3)*0.16rem)]",
        "pb-[calc(var(--grid-height,3)*0.1rem)]"
      )}>
        <div 
          className="font-bold tracking-tight bg-gradient-to-r from-foreground to-foreground/70 bg-clip-text text-transparent"
          style={{ 
            fontSize: 'clamp(1.25rem, calc(var(--grid-width, 3) * 0.5rem + var(--grid-height, 3) * 0.2rem), 2.5rem)',
            lineHeight: 'clamp(1.5rem, calc(var(--grid-width, 3) * 0.6rem + var(--grid-height, 3) * 0.25rem), 3rem)'
          }}
        >
          {value}
        </div>
        {description && (
          <p 
            className="text-muted-foreground mt-1 line-clamp-2"
            style={{ 
              fontSize: 'clamp(0.65rem, calc(var(--grid-width, 3) * 0.2rem), 0.8rem)',
              marginTop: 'clamp(0.25rem, calc(var(--grid-height, 3) * 0.1rem), 0.5rem)'
            }}
          >
            {description}
          </p>
        )}
      </CardContent>
      {trend && (
        <CardFooter className={cn(
          "px-[calc(var(--grid-width,3)*0.16rem)]",
          "pb-[calc(var(--grid-height,3)*0.15rem)]",
          "pt-0"
        )}>
          <Badge 
            variant={trend.isPositive ? "success" : "destructive"} 
            className={cn(
              "font-medium gap-1 shadow-sm animate-fade-in",
              trend.isPositive ? "bg-success/20 text-success hover:bg-success/30" : "bg-destructive/20 text-destructive hover:bg-destructive/30"
            )}
            style={{
              fontSize: 'clamp(0.6rem, calc(var(--grid-width, 3) * 0.18rem), 0.75rem)',
              padding: 'clamp(0.25rem, calc(var(--grid-width, 3) * 0.08rem), 0.5rem) clamp(0.5rem, calc(var(--grid-width, 3) * 0.12rem), 0.75rem)'
            }}
          >
            {trend.isPositive ? (
              <ArrowUp style={{ 
                width: 'clamp(0.6rem, calc(var(--grid-width, 3) * 0.15rem), 0.75rem)',
                height: 'clamp(0.6rem, calc(var(--grid-width, 3) * 0.15rem), 0.75rem)'
              }} />
            ) : (
              <ArrowDown style={{ 
                width: 'clamp(0.6rem, calc(var(--grid-width, 3) * 0.15rem), 0.75rem)',
                height: 'clamp(0.6rem, calc(var(--grid-width, 3) * 0.15rem), 0.75rem)'
              }} />
            )}
            {trend.value}%
          </Badge>
        </CardFooter>
      )}
    </Card>
  );
};
