// Simplified hook for accessing workout data from unified store
// Replaces useConsolidatedApp with a cleaner interface

import { useEffect } from "react";
import { useUnifiedDataStore } from "@/store/unifiedDataStore";
import {
  WorkoutLogEntry,
  Exercise,
  WorkoutTemplate,
  DailyMetrics,
} from "@/types/DataModels";

interface UseWorkoutDataReturn {
  // Data
  workoutLogs: WorkoutLogEntry[];
  exercises: Exercise[];
  workoutTemplates: WorkoutTemplate[];
  dailyMetrics: DailyMetrics[];

  // Loading states
  isLoading: boolean;
  isWorkoutLogsLoading: boolean;
  isExercisesLoading: boolean;
  isTemplatesLoading: boolean;

  // Error states
  error: string | null;
  workoutLogsError: string | null;
  exercisesError: string | null;
  templatesError: string | null;

  // Data fetched states
  isDataFetched: boolean;

  // Actions
  fetchWorkoutLogs: (forceRefresh?: boolean) => Promise<void>;
  fetchExercises: (forceRefresh?: boolean) => Promise<void>;
  fetchTemplates: (forceRefresh?: boolean) => Promise<void>;
  refreshAllData: () => Promise<void>;

  // CRUD operations for workout logs
  createWorkoutLog: (
    data: Omit<WorkoutLogEntry, "id" | "createdAt" | "updatedAt">,
  ) => Promise<{ success: boolean; error?: string }>;
  updateWorkoutLog: (
    id: string,
    data: Partial<WorkoutLogEntry>,
  ) => Promise<{ success: boolean; error?: string }>;
  deleteWorkoutLog: (
    id: string,
  ) => Promise<{ success: boolean; error?: string }>;

  // Computed data
  getUniqueExercises: () => string[];
  getUniqueMuscleGroups: () => string[];
  getExercisesByMuscleGroup: (muscleGroup: string) => Exercise[];
  getWorkoutLogsByMuscleGroup: (muscleGroup: string) => WorkoutLogEntry[];
  getWorkoutLogsByDateRange: (start: string, end: string) => WorkoutLogEntry[];
}

export const useWorkoutData = (): UseWorkoutDataReturn => {
  const store = useUnifiedDataStore();

  // Extract data from store - using actual store structure
  const {
    workoutLogs,
    exercises,
    templates: workoutTemplates,
    dailyMetrics,
    loading,
    errors,
    dataFetched,
    fetchWorkoutLogs,
    fetchExercises,
    fetchTemplates,
    fetchAllData,
    addWorkoutLog,
    editWorkoutLog,
    removeWorkoutLog,
    getExercisesByMuscleGroup: storeGetExercisesByMuscleGroup,
    getAllUniqueExerciseNames,
  } = store;

  // Auto-fetch data on mount if not already loaded
  useEffect(() => {
    const fetchInitialData = async () => {
      if (workoutLogs.length === 0 && !loading.workoutLogs) {
        await fetchWorkoutLogs();
      }
      if (exercises.length === 0 && !loading.exercises) {
        await fetchExercises();
      }
      if (workoutTemplates.length === 0 && !loading.templates) {
        await fetchTemplates();
      }
    };

    fetchInitialData();
  }, [
    workoutLogs.length,
    exercises.length,
    workoutTemplates.length,
    loading.workoutLogs,
    loading.exercises,
    loading.templates,
    fetchWorkoutLogs,
    fetchExercises,
    fetchTemplates,
  ]);

  // Loading states
  const isWorkoutLogsLoading = loading.workoutLogs || false;
  const isExercisesLoading = loading.exercises || false;
  const isTemplatesLoading = loading.templates || false;
  const isLoading =
    isWorkoutLogsLoading || isExercisesLoading || isTemplatesLoading;

  // Error states
  const workoutLogsError = errors.workoutLogs || null;
  const exercisesError = errors.exercises || null;
  const templatesError = errors.templates || null;
  const error = workoutLogsError || exercisesError || templatesError;

  // Data fetched state
  const isDataFetched =
    dataFetched.workoutLogs || dataFetched.exercises || dataFetched.templates;

  // CRUD operations with error handling
  const createWorkoutLog = async (
    data: Omit<WorkoutLogEntry, "id" | "createdAt" | "updatedAt">,
  ) => {
    try {
      const result = await addWorkoutLog(data);
      return {
        success: !!result,
        error: result ? null : "Failed to create workout log",
      };
    } catch (error) {
      return {
        success: false,
        error: error instanceof Error ? error.message : "Unknown error",
      };
    }
  };

  const updateWorkoutLog = async (
    id: string,
    data: Partial<WorkoutLogEntry>,
  ) => {
    try {
      const logToUpdate = workoutLogs.find((log) => log.id === id);
      if (!logToUpdate) {
        return { success: false, error: "Workout log not found" };
      }
      await editWorkoutLog({ ...logToUpdate, ...data });
      return { success: true };
    } catch (error) {
      return {
        success: false,
        error: error instanceof Error ? error.message : "Unknown error",
      };
    }
  };

  const deleteWorkoutLog = async (id: string) => {
    try {
      await removeWorkoutLog(id);
      return { success: true };
    } catch (error) {
      return {
        success: false,
        error: error instanceof Error ? error.message : "Unknown error",
      };
    }
  };

  // Computed data functions
  const getUniqueExercises = (): string[] => {
    const exerciseNames = workoutLogs.map((log) => log.exercise);
    return [...new Set(exerciseNames)].filter(Boolean).sort();
  };

  const getUniqueMuscleGroups = (): string[] => {
    const muscleGroups = workoutLogs.map((log) => log.muscleGroup);
    return [...new Set(muscleGroups)].filter(Boolean).sort();
  };

  const getExercisesByMuscleGroup = (muscleGroup: string): Exercise[] => {
    // Return exercises that match the muscle group
    return exercises.filter(
      (exercise) =>
        exercise.muscleGroup.toLowerCase() === muscleGroup.toLowerCase(),
    );
  };

  const getWorkoutLogsByMuscleGroup = (
    muscleGroup: string,
  ): WorkoutLogEntry[] => {
    return workoutLogs.filter(
      (log) => log.muscleGroup.toLowerCase() === muscleGroup.toLowerCase(),
    );
  };

  const getWorkoutLogsByDateRange = (
    start: string,
    end: string,
  ): WorkoutLogEntry[] => {
    return workoutLogs.filter((log) => {
      const logDate = new Date(log.date);
      const startDate = new Date(start);
      const endDate = new Date(end);
      return logDate >= startDate && logDate <= endDate;
    });
  };

  return {
    // Data
    workoutLogs: workoutLogs || [],
    exercises: exercises || [],
    workoutTemplates: workoutTemplates || [],
    dailyMetrics: dailyMetrics || [],

    // Loading states
    isLoading,
    isWorkoutLogsLoading,
    isExercisesLoading,
    isTemplatesLoading,

    // Error states
    error,
    workoutLogsError,
    exercisesError,
    templatesError,

    // Data fetched states
    isDataFetched,

    // Actions
    fetchWorkoutLogs,
    fetchExercises,
    fetchTemplates,
    refreshAllData: fetchAllData,

    // CRUD operations
    createWorkoutLog,
    updateWorkoutLog,
    deleteWorkoutLog,

    // Computed data
    getUniqueExercises,
    getUniqueMuscleGroups,
    getExercisesByMuscleGroup,
    getWorkoutLogsByMuscleGroup,
    getWorkoutLogsByDateRange,
  };
};

// Backward compatibility aliases
export const useWorkoutLogs = useWorkoutData;
export const useConsolidatedApp = useWorkoutData;
