import React, { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from '@/components/ui/dialog';
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
} from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { type Person } from '@/types/Health';
import { Trash2, Edit, Users, Phone, Mail, Calendar, AlertCircle } from 'lucide-react';

// Simple Avatar component replacement
const Avatar: React.FC<{ children: React.ReactNode; className?: string }> = ({ children, className = '' }) => (
  <div className={`flex items-center justify-center rounded-full bg-gray-200 ${className}`}>
    {children}
  </div>
);

const AvatarFallback: React.FC<{ children: React.ReactNode; className?: string }> = ({ children, className = '' }) => (
  <span className={`text-sm font-medium text-gray-600 ${className}`}>{children}</span>
);

const AvatarImage: React.FC<{ src?: string | undefined; alt?: string }> = ({ src, alt }) => {
  if (!src) return null;
  return <img src={src} alt={alt} className="h-full w-full rounded-full object-cover" />;
};

interface PersonManagerProps {
  people: Person[];
  onPersonAdd: (person: Omit<Person, 'id' | 'createdAt' | 'updatedAt'>) => Promise<boolean>;
  onPersonUpdate: (id: string, person: Partial<Person>) => Promise<boolean>;
  onPersonDelete: (id: string) => Promise<boolean>;
}

export const PersonManager: React.FC<PersonManagerProps> = ({ 
  people, 
  onPersonAdd, 
  onPersonUpdate, 
  onPersonDelete 
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const [isAddingPerson, setIsAddingPerson] = useState(false);
  const [editingPerson, setEditingPerson] = useState<Person | null>(null);
  const [formData, setFormData] = useState({
    name: '',
    relationship: 'family' as 'self' | 'family' | 'friend',
    email: '',
    phone: '',
    dateOfBirth: '',
    emergencyContact: false,
    notes: '',
    avatar: ''
  });

  const resetForm = () => {
    setFormData({
      name: '',
      relationship: 'family',
      email: '',
      phone: '',
      dateOfBirth: '',
      emergencyContact: false,
      notes: '',
      avatar: ''
    });
  };

  const handleStartAdd = () => {
    resetForm();
    setEditingPerson(null);
    setIsAddingPerson(true);
  };

  const handleStartEdit = (person: Person) => {
    setFormData({
      name: person.name,
      relationship: person.relationship,
      email: person.email || '',
      phone: person.phone || '',
      dateOfBirth: person.dateOfBirth || '',
      emergencyContact: person.emergencyContact || false,
      notes: person.notes || '',
      avatar: person.avatar || ''
    });
    setEditingPerson(person);
    setIsAddingPerson(true);
  };  const handleSubmit = async () => {
    if (!formData.name.trim()) return;

    const personData = {
      name: formData.name,
      relationship: formData.relationship,
      ...(formData.email && { email: formData.email }),
      ...(formData.phone && { phone: formData.phone }),
      ...(formData.dateOfBirth && { dateOfBirth: formData.dateOfBirth }),
      ...(formData.notes && { notes: formData.notes }),
      ...(formData.avatar && { avatar: formData.avatar }),
      ...(formData.emergencyContact && { emergencyContact: formData.emergencyContact })
    };

    let success = false;
    if (editingPerson) {
      success = await onPersonUpdate(editingPerson.id, personData);
    } else {
      success = await onPersonAdd(personData);
    }

    if (success) {
      setIsAddingPerson(false);
      setEditingPerson(null);
      resetForm();
    }
  };

  const handleDelete = async (id: string) => {
    if (confirm('Are you sure you want to remove this person? This cannot be undone.')) {
      await onPersonDelete(id);
    }
  };

  const getPersonInitials = (name: string) => {
    return name.split(' ').map(n => n[0]).join('').toUpperCase().slice(0, 2);
  };

  const getRelationshipColor = (relationship: string) => {
    switch (relationship) {
      case 'self': return 'bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-300';
      case 'family': return 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-300';
      case 'friend': return 'bg-purple-100 text-purple-800 dark:bg-purple-900 dark:text-purple-300';
      default: return 'bg-gray-100 text-gray-800 dark:bg-gray-900 dark:text-gray-300';
    }
  };
  return (
    <>
      <Button onClick={() => setIsOpen(true)}>
        <Users className="h-4 w-4 mr-2" />
        Manage People
      </Button>

      <Dialog open={isOpen} onOpenChange={setIsOpen}>
        <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto">
          <DialogHeader>
            <DialogTitle className="flex items-center gap-2">
              <Users className="h-5 w-5" />
              Manage People
            </DialogTitle>
            <DialogDescription>
              Add and manage family members and friends for health tracking.
            </DialogDescription>
          </DialogHeader>

          <div className="space-y-6">
            {/* Add Person Button */}
            <div className="flex justify-between items-center">
              <h3 className="text-lg font-semibold">People ({people.length})</h3>
              <Button onClick={handleStartAdd} className="flex items-center gap-2">
                <Users className="h-4 w-4" />
                Add Person
              </Button>
            </div>

            {/* People List */}
            <div className="grid gap-4 md:grid-cols-2">
              {people.map((person) => (
                <Card key={person.id}>
                  <CardHeader className="pb-3">
                    <div className="flex items-start justify-between">
                      <div className="flex items-center gap-3">
                        <Avatar className="h-10 w-10">
                          <AvatarImage src={person.avatar} />
                          <AvatarFallback>
                            {getPersonInitials(person.name)}
                          </AvatarFallback>
                        </Avatar>
                        <div>
                          <CardTitle className="text-base">{person.name}</CardTitle>
                          <div className="flex items-center gap-2 mt-1">
                            <Badge 
                              variant="secondary" 
                              className={`text-xs ${getRelationshipColor(person.relationship)}`}
                            >
                              {person.relationship}
                            </Badge>
                            {person.emergencyContact && (
                              <Badge variant="destructive" className="text-xs">
                                <AlertCircle className="h-3 w-3 mr-1" />
                                Emergency
                              </Badge>
                            )}
                          </div>
                        </div>
                      </div>
                      <div className="flex gap-1">
                        <Button
                          variant="ghost"
                          size="sm"
                          onClick={() => handleStartEdit(person)}
                        >
                          <Edit className="h-4 w-4" />
                        </Button>
                        <Button
                          variant="ghost"
                          size="sm"
                          onClick={() => handleDelete(person.id)}
                        >
                          <Trash2 className="h-4 w-4" />
                        </Button>
                      </div>
                    </div>
                  </CardHeader>
                  <CardContent className="pt-0">
                    <div className="space-y-2 text-sm text-muted-foreground">
                      {person.email && (
                        <div className="flex items-center gap-2">
                          <Mail className="h-4 w-4" />
                          {person.email}
                        </div>
                      )}
                      {person.phone && (
                        <div className="flex items-center gap-2">
                          <Phone className="h-4 w-4" />
                          {person.phone}
                        </div>
                      )}
                      {person.dateOfBirth && (
                        <div className="flex items-center gap-2">
                          <Calendar className="h-4 w-4" />
                          {person.dateOfBirth}
                        </div>
                      )}
                      {person.notes && (
                        <p className="text-xs mt-2 line-clamp-2">{person.notes}</p>
                      )}
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>

            {people.length === 0 && (
              <div className="text-center py-8 text-muted-foreground">
                <Users className="h-12 w-12 mx-auto mb-4 opacity-50" />
                <p>No people added yet</p>
                <p className="text-sm">Add family members and friends to track their health data</p>
              </div>
            )}
          </div>

          <DialogFooter>            <Button variant="outline" onClick={() => setIsOpen(false)}>
              Close
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>

      {/* Add/Edit Person Dialog */}
      <Dialog open={isAddingPerson} onOpenChange={setIsAddingPerson}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>
              {editingPerson ? 'Edit Person' : 'Add New Person'}
            </DialogTitle>
            <DialogDescription>
              {editingPerson 
                ? 'Update the person\'s information' 
                : 'Add a family member or friend to track their health data'
              }
            </DialogDescription>
          </DialogHeader>

          <div className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="name">Name *</Label>
              <Input
                id="name"
                value={formData.name}
                onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                placeholder="Enter name"
              />
            </div>

            <div className="space-y-2">
              <Label htmlFor="relationship">Relationship</Label>
              <Select
                value={formData.relationship}
                onValueChange={(value: 'self' | 'family' | 'friend') => 
                  setFormData({ ...formData, relationship: value })
                }
              >
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="family">Family</SelectItem>
                  <SelectItem value="friend">Friend</SelectItem>
                  <SelectItem value="self">Self</SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="email">Email</Label>
                <Input
                  id="email"
                  type="email"
                  value={formData.email}
                  onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                  placeholder="email@example.com"
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="phone">Phone</Label>
                <Input
                  id="phone"
                  value={formData.phone}
                  onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
                  placeholder="Phone number"
                />
              </div>
            </div>

            <div className="space-y-2">
              <Label htmlFor="dateOfBirth">Date of Birth</Label>
              <Input
                id="dateOfBirth"
                type="date"
                value={formData.dateOfBirth}
                onChange={(e) => setFormData({ ...formData, dateOfBirth: e.target.value })}
              />
            </div>

            <div className="space-y-2">
              <Label htmlFor="notes">Notes</Label>
              <Textarea
                id="notes"
                value={formData.notes}
                onChange={(e) => setFormData({ ...formData, notes: e.target.value })}
                placeholder="Additional notes..."
                rows={3}
              />
            </div>

            <div className="flex items-center space-x-2">
              <input
                type="checkbox"
                id="emergencyContact"
                checked={formData.emergencyContact}
                onChange={(e) => setFormData({ ...formData, emergencyContact: e.target.checked })}
                className="rounded"
              />
              <Label htmlFor="emergencyContact">Emergency Contact</Label>
            </div>
          </div>

          <DialogFooter>
            <Button 
              variant="outline" 
              onClick={() => {
                setIsAddingPerson(false);
                setEditingPerson(null);
                resetForm();
              }}
            >
              Cancel
            </Button>
            <Button onClick={handleSubmit} disabled={!formData.name.trim()}>
              {editingPerson ? 'Update' : 'Add'} Person
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </>
  );
};
