import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Badge } from '@/components/ui/badge';
import { 
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger
} from '@/components/ui/dropdown-menu';
import { 
  FileText, 
  Download, 
  Eye, 
  Trash2, 
  MoreVertical, 
  Search,
  Filter,
  Calendar,
  Lock,
  FolderOpen,
  Image,
  File
} from 'lucide-react';
import { useHealthStore } from '@/store/healthStore';
import { toast } from 'sonner';
import { format } from 'date-fns';

const CATEGORY_LABELS: Record<string, string> = {
  'medical-record': 'Medical Record',
  'lab-result': 'Lab Result',
  'prescription': 'Prescription',
  'insurance': 'Insurance',
  'report': 'Report',
  'other': 'Other'
};

const CATEGORY_COLORS: Record<string, string> = {
  'medical-record': 'bg-blue-100 text-blue-800',
  'lab-result': 'bg-purple-100 text-purple-800',
  'prescription': 'bg-green-100 text-green-800',
  'insurance': 'bg-orange-100 text-orange-800',
  'report': 'bg-pink-100 text-pink-800',
  'other': 'bg-gray-100 text-gray-800'
};

export const DocumentList: React.FC = () => {
  const { 
    currentPersonId, 
    getPersonDocuments, 
    deleteDocument,
    downloadDocument,
    refreshDocuments,
    people,
    isLoading 
  } = useHealthStore();
  
  const currentPerson = people.find(p => p.id === currentPersonId);
  const documents = getPersonDocuments();
  
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState<string>('all');
  const [showConfidentialOnly, setShowConfidentialOnly] = useState(false);
  const [deletingDocId, setDeletingDocId] = useState<string | null>(null);

  useEffect(() => {
    // Refresh documents when component mounts or person changes
    refreshDocuments();
  }, [currentPersonId, refreshDocuments]);

  const getFileIcon = (fileType: string) => {
    if (fileType.startsWith('image/')) {
      return <Image className="h-4 w-4" />;
    }
    if (fileType.includes('pdf')) {
      return <FileText className="h-4 w-4 text-red-600" />;
    }
    return <File className="h-4 w-4" />;
  };

  const formatFileSize = (bytes: number): string => {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  };

  const filteredDocuments = documents.filter(doc => {
    // Search filter
    if (searchTerm && !doc.title.toLowerCase().includes(searchTerm.toLowerCase()) &&
        !doc.description?.toLowerCase().includes(searchTerm.toLowerCase()) &&
        !doc.tags?.some(tag => tag.toLowerCase().includes(searchTerm.toLowerCase()))) {
      return false;
    }
    
    // Category filter
    if (selectedCategory !== 'all' && doc.category !== selectedCategory) {
      return false;
    }
    
    // Confidential filter
    if (showConfidentialOnly && !doc.isConfidential) {
      return false;
    }
    
    return true;
  });

  const handleView = (doc: any) => {
    if (doc.driveUrl) {
      window.open(doc.driveUrl, '_blank');
    } else {
      toast.error('Document URL not available');
    }
  };

  const handleDownload = async (docId: string) => {
    try {
      await downloadDocument(docId);
    } catch (error) {
      toast.error('Failed to download document');
    }
  };

  const handleDelete = async (docId: string) => {
    if (!confirm('Are you sure you want to delete this document? This action cannot be undone.')) {
      return;
    }
    
    setDeletingDocId(docId);
    
    try {
      const success = await deleteDocument(docId);
      if (success) {
        toast.success('Document deleted successfully');
      } else {
        toast.error('Failed to delete document');
      }
    } catch (error) {
      toast.error('Error deleting document');
    } finally {
      setDeletingDocId(null);
    }
  };

  return (
    <Card>
      <CardHeader>
        <div className="flex items-center justify-between">
          <CardTitle className="flex items-center gap-2">
            <FolderOpen className="h-5 w-5" />
            {currentPerson?.name || 'Your'} Health Documents ({filteredDocuments.length})
          </CardTitle>
          <Button
            variant="outline"
            size="sm"
            onClick={() => refreshDocuments()}
            disabled={isLoading}
          >
            Refresh
          </Button>
        </div>
      </CardHeader>
      <CardContent>
        {/* Filters */}
        <div className="space-y-4 mb-6">
          <div className="flex flex-col sm:flex-row gap-2">
            <div className="relative flex-1">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-500" />
              <Input
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                placeholder="Search documents..."
                className="pl-10"
              />
            </div>
            
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <Button variant="outline" className="min-w-[140px]">
                  <Filter className="h-4 w-4 mr-2" />
                  {selectedCategory === 'all' ? 'All Categories' : CATEGORY_LABELS[selectedCategory]}
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent>
                <DropdownMenuLabel>Filter by Category</DropdownMenuLabel>
                <DropdownMenuSeparator />
                <DropdownMenuItem onClick={() => setSelectedCategory('all')}>
                  All Categories
                </DropdownMenuItem>
                {Object.entries(CATEGORY_LABELS).map(([value, label]) => (
                  <DropdownMenuItem key={value} onClick={() => setSelectedCategory(value)}>
                    {label}
                  </DropdownMenuItem>
                ))}
              </DropdownMenuContent>
            </DropdownMenu>
            
            <Button
              variant={showConfidentialOnly ? "default" : "outline"}
              onClick={() => setShowConfidentialOnly(!showConfidentialOnly)}
              className="min-w-[140px]"
            >
              <Lock className="h-4 w-4 mr-2" />
              Confidential Only
            </Button>
          </div>
        </div>
        
        {/* Document List */}
        {isLoading ? (
          <div className="text-center py-8">
            <div className="inline-flex items-center">
              <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-gray-900"></div>
              <span className="ml-3">Loading documents...</span>
            </div>
          </div>
        ) : filteredDocuments.length === 0 ? (
          <div className="text-center py-8">
            <FileText className="mx-auto h-12 w-12 text-gray-400" />
            <h3 className="mt-2 text-sm font-medium text-gray-900">No documents found</h3>
            <p className="mt-1 text-sm text-gray-500">
              {searchTerm || selectedCategory !== 'all' 
                ? 'Try adjusting your filters' 
                : 'Upload your first health document to get started'}
            </p>
          </div>
        ) : (
          <div className="space-y-3">
            {filteredDocuments.map((doc) => (
              <div
                key={doc.id}
                className="flex items-center justify-between p-4 border rounded-lg hover:bg-gray-50 transition-colors"
              >
                <div className="flex items-start gap-3 flex-1">
                  {getFileIcon(doc.fileType)}
                  <div className="flex-1 min-w-0">
                    <div className="flex items-center gap-2 flex-wrap">
                      <h4 className="font-medium text-sm">{doc.title}</h4>
                      {doc.isConfidential && (
                        <Lock className="h-3 w-3 text-orange-600" />
                      )}
                      <Badge className={`text-xs ${CATEGORY_COLORS[doc.category] || CATEGORY_COLORS.other}`}>
                        {CATEGORY_LABELS[doc.category] || 'Other'}
                      </Badge>
                    </div>
                    
                    {doc.description && (
                      <p className="text-xs text-gray-600 mt-1 line-clamp-2">{doc.description}</p>
                    )}
                    
                    <div className="flex items-center gap-4 mt-2 text-xs text-gray-500">
                      <span className="flex items-center gap-1">
                        <Calendar className="h-3 w-3" />
                        {format(new Date(doc.uploadDate), 'MMM d, yyyy')}
                      </span>
                      <span>{formatFileSize(doc.fileSize)}</span>
                      {doc.tags && doc.tags.length > 0 && (
                        <div className="flex items-center gap-1">
                          {doc.tags.slice(0, 3).map((tag, index) => (
                            <Badge key={index} variant="secondary" className="text-xs">
                              {tag}
                            </Badge>
                          ))}
                          {doc.tags.length > 3 && (
                            <span className="text-gray-500">+{doc.tags.length - 3}</span>
                          )}
                        </div>
                      )}
                    </div>
                  </div>
                </div>
                
                <DropdownMenu>
                  <DropdownMenuTrigger asChild>
                    <Button
                      variant="ghost"
                      size="sm"
                      disabled={deletingDocId === doc.id}
                    >
                      <MoreVertical className="h-4 w-4" />
                    </Button>
                  </DropdownMenuTrigger>
                  <DropdownMenuContent align="end">
                    <DropdownMenuItem onClick={() => handleView(doc)}>
                      <Eye className="h-4 w-4 mr-2" />
                      View
                    </DropdownMenuItem>
                    <DropdownMenuItem onClick={() => handleDownload(doc.id)}>
                      <Download className="h-4 w-4 mr-2" />
                      Download
                    </DropdownMenuItem>
                    <DropdownMenuSeparator />
                    <DropdownMenuItem 
                      onClick={() => handleDelete(doc.id)}
                      className="text-red-600"
                    >
                      <Trash2 className="h-4 w-4 mr-2" />
                      Delete
                    </DropdownMenuItem>
                  </DropdownMenuContent>
                </DropdownMenu>
              </div>
            ))}
          </div>
        )}
      </CardContent>
    </Card>
  );
}; 