import React from 'react';
import { LineChart } from 'lucide-react';
import { Card, CardContent } from '@/components/ui/card';

export const AnalyticsActionCard: React.FC = () => {
  const handleAnalyticsClick = () => {
    // Add navigation to analytics page when implemented
    console.log('Navigate to analytics');
  };

  return (
    <Card 
      className="transition-all hover:shadow-md hover:translate-y-[-2px] duration-300 cursor-pointer"
      onClick={handleAnalyticsClick}
    >
      <CardContent className="p-5">
        <div className="flex items-center gap-4">
          <div className="h-10 w-10 rounded-full bg-accent-light flex items-center justify-center text-accent">
            <LineChart className="h-5 w-5" />
          </div>
          <div>
            <h4 className="font-medium">Analytics</h4>
            <p className="text-sm text-muted-foreground">View detailed stats</p>
          </div>
        </div>
      </CardContent>
    </Card>
  );
}; 