import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Plus, ChefHat, Clock, Users, BookOpen, Edit2, Trash2, Copy } from 'lucide-react';
import { toast } from 'sonner';
import { createMealPlan, getMealPlans, updateMealPlan, deleteMealPlan } from '@/lib/spreadsheetAPI';
import { format, addDays, startOfWeek } from 'date-fns';

interface MealPlan {
  id?: string;
  rowIndex?: number;
  date: string;
  meal: 'breakfast' | 'lunch' | 'dinner' | 'snack';
  recipeName: string;
  ingredients: string;
  instructions: string;
  prepTime: number;
  cookTime: number;
  servings: number;
  calories: number;
  protein: number;
  carbs: number;
  fat: number;
  tags: string[];
  notes?: string;
}

interface MealTemplate {
  id: string;
  name: string;
  ingredients: string;
  instructions: string;
  prepTime: number;
  cookTime: number;
  servings: number;
  calories: number;
  protein: number;
  carbs: number;
  fat: number;
  tags: string[];
  category: 'breakfast' | 'lunch' | 'dinner' | 'snack';
}

const MealPlanningPage: React.FC = () => {  const [mealPlans, setMealPlans] = useState<MealPlan[]>([]);
  const [templates, setTemplates] = useState<MealTemplate[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [showAddForm, setShowAddForm] = useState(false);  const [editingPlan, setEditingPlan] = useState<MealPlan | null>(null);
  const [selectedWeek, setSelectedWeek] = useState(startOfWeek(new Date()));
  const [activeTab, setActiveTab] = useState('planner');

  const [formData, setFormData] = useState<MealPlan>({
    date: format(new Date(), 'yyyy-MM-dd'),
    meal: 'breakfast',
    recipeName: '',
    ingredients: '',
    instructions: '',
    prepTime: 0,
    cookTime: 0,
    servings: 1,
    calories: 0,
    protein: 0,
    carbs: 0,
    fat: 0,
    tags: [],
    notes: ''
  });
  const weekDays = Array.from({ length: 7 }, (_, i) => addDays(selectedWeek, i));
  const mealTypes = ['breakfast', 'lunch', 'dinner', 'snack'] as const;  useEffect(() => {    const loadData = async () => {
      setIsLoading(true);
      try {
        const data = await getMealPlans();
        const weekStart = format(selectedWeek, 'yyyy-MM-dd');
        const weekEnd = format(addDays(selectedWeek, 6), 'yyyy-MM-dd');
        
        const filteredPlans = data
          .map((plan: Record<string, unknown>, index: number) => ({ ...plan, rowIndex: index + 2 }))
          .filter((plan: Record<string, unknown>) => 
            (plan.date as string) >= weekStart && (plan.date as string) <= weekEnd
          ) as MealPlan[];
        setMealPlans(filteredPlans);      } catch (error) {
        console.error('Error loading meal plans:', error);
        toast.error("Failed to load meal plans. Please try again.");
      } finally {
        setIsLoading(false);
      }
    };
      loadData();
    loadTemplates();
  }, [selectedWeek]);const loadMealPlans = async () => {
    setIsLoading(true);
    try {
      const data = await getMealPlans();
      const weekStart = format(selectedWeek, 'yyyy-MM-dd');
      const weekEnd = format(addDays(selectedWeek, 6), 'yyyy-MM-dd');
        const filteredPlans = data
        .map((plan: Record<string, unknown>, index: number) => ({ ...plan, rowIndex: index + 2 }))
        .filter((plan: Record<string, unknown>) => 
          (plan.date as string) >= weekStart && (plan.date as string) <= weekEnd        ) as MealPlan[];
      setMealPlans(filteredPlans);
    } catch (error) {
      console.error('Error loading meal plans:', error);
      toast.error("Failed to load meal plans. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  const loadTemplates = async () => {
    try {
      // In a real implementation, this would load from a templates API
      // For now, we'll use some sample templates
      const sampleTemplates: MealTemplate[] = [
        {
          id: '1',
          name: 'Overnight Oats',
          ingredients: '1 cup rolled oats, 1 cup milk, 1 tbsp chia seeds, 1 tbsp honey, 1/2 cup berries',
          instructions: 'Mix all ingredients in a jar. Refrigerate overnight. Enjoy cold in the morning.',
          prepTime: 5,
          cookTime: 0,
          servings: 1,
          calories: 350,
          protein: 15,
          carbs: 55,
          fat: 8,
          tags: ['healthy', 'quick', 'meal-prep'],
          category: 'breakfast'
        },
        {
          id: '2',
          name: 'Grilled Chicken Salad',
          ingredients: '6oz chicken breast, 2 cups mixed greens, 1/4 cup cherry tomatoes, 1/4 avocado, 2 tbsp olive oil vinaigrette',
          instructions: 'Grill chicken breast. Assemble salad with greens, tomatoes, and avocado. Top with sliced chicken and dressing.',
          prepTime: 10,
          cookTime: 15,
          servings: 1,
          calories: 450,
          protein: 40,
          carbs: 12,
          fat: 25,
          tags: ['healthy', 'high-protein', 'gluten-free'],
          category: 'lunch'
        }
      ];
      setTemplates(sampleTemplates);
    } catch (error) {
      console.error('Error loading templates:', error);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);    try {
      if (editingPlan?.rowIndex) {
        await updateMealPlan(editingPlan.rowIndex, formData);
        toast.success("Meal plan updated successfully!");
      } else {
        await createMealPlan(formData);
        toast.success("Meal plan added successfully!");
      }
      
      resetForm();
      loadMealPlans();
    } catch (error) {
      console.error('Error saving meal plan:', error);
      toast.error("Failed to save meal plan. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  const handleEdit = (plan: MealPlan) => {
    setEditingPlan(plan);
    setFormData(plan);
    setShowAddForm(true);
  };
  const handleDelete = async (rowIndex: number) => {
    if (!confirm('Are you sure you want to delete this meal plan?')) return;

    setIsLoading(true);    try {
      await deleteMealPlan(rowIndex);
      toast.success("Meal plan deleted successfully!");
      loadMealPlans();
    } catch (error) {
      console.error('Error deleting meal plan:', error);
      toast.error("Failed to delete meal plan. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  const handleUseTemplate = (template: MealTemplate) => {
    setFormData({
      date: format(new Date(), 'yyyy-MM-dd'),
      meal: template.category,
      recipeName: template.name,
      ingredients: template.ingredients,
      instructions: template.instructions,
      prepTime: template.prepTime,
      cookTime: template.cookTime,
      servings: template.servings,
      calories: template.calories,
      protein: template.protein,
      carbs: template.carbs,
      fat: template.fat,
      tags: template.tags,
      notes: ''
    });
    setShowAddForm(true);
  };

  const resetForm = () => {
    setFormData({
      date: format(new Date(), 'yyyy-MM-dd'),
      meal: 'breakfast',
      recipeName: '',
      ingredients: '',
      instructions: '',
      prepTime: 0,
      cookTime: 0,
      servings: 1,
      calories: 0,
      protein: 0,
      carbs: 0,
      fat: 0,
      tags: [],
      notes: ''
    });
    setEditingPlan(null);
    setShowAddForm(false);
  };

  const getMealForDateAndType = (date: Date, mealType: string) => {
    const dateStr = format(date, 'yyyy-MM-dd');
    return mealPlans.find(plan => plan.date === dateStr && plan.meal === mealType);
  };

  const getMealTypeColor = (meal: string) => {
    switch (meal) {
      case 'breakfast': return 'bg-orange-100 text-orange-800 border-orange-200';
      case 'lunch': return 'bg-green-100 text-green-800 border-green-200';
      case 'dinner': return 'bg-blue-100 text-blue-800 border-blue-200';
      case 'snack': return 'bg-purple-100 text-purple-800 border-purple-200';
      default: return 'bg-gray-100 text-gray-800 border-gray-200';
    }
  };

  return (
    <div className="p-6 space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold text-primary">Meal Planning</h1>
          <p className="text-gray-600">Plan your meals and track your recipes</p>
        </div>        <div className="flex gap-2">          <Button
            variant="outline"
            onClick={() => toast.info("Template feature coming soon!")}
            className="flex items-center gap-2"
          >
            <BookOpen className="h-4 w-4" />
            Templates
          </Button>
          <Button
            onClick={() => setShowAddForm(true)}
            className="flex items-center gap-2"
          >
            <Plus className="h-4 w-4" />
            Add Meal
          </Button>
        </div>
      </div>

      <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
        <TabsList className="grid w-full grid-cols-3">
          <TabsTrigger value="planner">Weekly Planner</TabsTrigger>
          <TabsTrigger value="templates">Recipe Templates</TabsTrigger>
          <TabsTrigger value="shopping">Shopping List</TabsTrigger>
        </TabsList>

        <TabsContent value="planner" className="space-y-6">
          {/* Week Navigation */}
          <div className="flex items-center justify-between">
            <Button
              variant="outline"
              onClick={() => setSelectedWeek(addDays(selectedWeek, -7))}
            >
              Previous Week
            </Button>
            <h2 className="text-xl font-semibold">
              Week of {format(selectedWeek, 'MMM d')} - {format(addDays(selectedWeek, 6), 'MMM d, yyyy')}
            </h2>
            <Button
              variant="outline"
              onClick={() => setSelectedWeek(addDays(selectedWeek, 7))}
            >
              Next Week
            </Button>
          </div>

          {/* Weekly Meal Grid */}
          <div className="grid grid-cols-1 lg:grid-cols-7 gap-4">
            {weekDays.map((day) => (
              <Card key={format(day, 'yyyy-MM-dd')} className="min-h-[400px]">
                <CardHeader className="pb-2">
                  <CardTitle className="text-center text-sm">
                    {format(day, 'EEE')}
                    <br />
                    <span className="text-lg">{format(day, 'd')}</span>
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-2">
                  {mealTypes.map((mealType) => {
                    const meal = getMealForDateAndType(day, mealType);
                    return (
                      <div key={mealType} className="space-y-1">
                        <div className="text-xs font-medium text-gray-600 uppercase tracking-wide">
                          {mealType}
                        </div>
                        {meal ? (
                          <div className={`p-2 rounded-lg border ${getMealTypeColor(mealType)}`}>
                            <div className="font-medium text-xs truncate">{meal.recipeName}</div>
                            <div className="text-xs text-gray-600 flex items-center gap-1">
                              <Clock className="h-3 w-3" />
                              {meal.prepTime + meal.cookTime}min
                            </div>
                            <div className="text-xs text-gray-600">
                              {meal.calories} cal
                            </div>
                            <div className="flex gap-1 mt-1">
                              <Button
                                variant="ghost"
                                size="sm"
                                className="h-6 w-6 p-0"
                                onClick={() => handleEdit(meal)}
                              >
                                <Edit2 className="h-3 w-3" />
                              </Button>
                              <Button
                                variant="ghost"
                                size="sm"
                                className="h-6 w-6 p-0"
                                onClick={() => meal.rowIndex && handleDelete(meal.rowIndex)}
                              >
                                <Trash2 className="h-3 w-3" />
                              </Button>
                            </div>
                          </div>
                        ) : (
                          <Button
                            variant="ghost"
                            className="w-full h-12 border-2 border-dashed border-gray-300 text-gray-500 hover:border-gray-400"
                            onClick={() => {
                              setFormData({
                                ...formData,
                                date: format(day, 'yyyy-MM-dd'),
                                meal: mealType
                              });
                              setShowAddForm(true);
                            }}
                          >
                            <Plus className="h-4 w-4" />
                          </Button>
                        )}
                      </div>
                    );
                  })}
                </CardContent>
              </Card>
            ))}
          </div>
        </TabsContent>

        <TabsContent value="templates">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {templates.map((template) => (
              <Card key={template.id} className="hover:shadow-md transition-shadow">
                <CardHeader>
                  <div className="flex items-start justify-between">
                    <div>
                      <CardTitle className="text-lg">{template.name}</CardTitle>
                      <Badge className={getMealTypeColor(template.category)} variant="outline">
                        {template.category}
                      </Badge>
                    </div>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => handleUseTemplate(template)}
                    >
                      <Copy className="h-4 w-4" />
                    </Button>
                  </div>
                </CardHeader>
                <CardContent>
                  <div className="space-y-2 text-sm">
                    <div className="flex items-center gap-4 text-gray-600">
                      <span className="flex items-center gap-1">
                        <Clock className="h-4 w-4" />
                        {template.prepTime + template.cookTime}min
                      </span>
                      <span className="flex items-center gap-1">
                        <Users className="h-4 w-4" />
                        {template.servings} serving{template.servings > 1 ? 's' : ''}
                      </span>
                    </div>
                    <div className="text-gray-600">
                      {template.calories} calories | {template.protein}g protein
                    </div>
                    <div className="flex flex-wrap gap-1">
                      {template.tags.map((tag) => (
                        <Badge key={tag} variant="secondary" className="text-xs">
                          {tag}
                        </Badge>
                      ))}
                    </div>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </TabsContent>

        <TabsContent value="shopping">
          <Card>
            <CardHeader>
              <CardTitle>Shopping List</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-center py-8">
                <ChefHat className="mx-auto h-12 w-12 text-gray-400" />
                <h3 className="mt-2 text-sm font-medium text-gray-900">Shopping List</h3>
                <p className="mt-1 text-sm text-gray-500">
                  Generate shopping lists based on your weekly meal plans.
                </p>
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>

      {/* Add/Edit Meal Form Modal */}
      {showAddForm && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
          <Card className="w-full max-w-2xl max-h-[90vh] overflow-y-auto">
            <CardHeader>
              <CardTitle>
                {editingPlan ? 'Edit Meal Plan' : 'Add Meal Plan'}
              </CardTitle>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSubmit} className="space-y-4">
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="date">Date</Label>
                    <Input
                      id="date"
                      type="date"
                      value={formData.date}
                      onChange={(e) => setFormData({ ...formData, date: e.target.value })}
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="meal">Meal</Label>
                    <Select 
                      value={formData.meal} 
                      onValueChange={(value: 'breakfast' | 'lunch' | 'dinner' | 'snack') => setFormData({ ...formData, meal: value })}
                    >
                      <SelectTrigger>
                        <SelectValue />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="breakfast">Breakfast</SelectItem>
                        <SelectItem value="lunch">Lunch</SelectItem>
                        <SelectItem value="dinner">Dinner</SelectItem>
                        <SelectItem value="snack">Snack</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                </div>

                <div>
                  <Label htmlFor="recipeName">Recipe Name</Label>
                  <Input
                    id="recipeName"
                    value={formData.recipeName}
                    onChange={(e) => setFormData({ ...formData, recipeName: e.target.value })}
                    placeholder="e.g., Grilled Chicken with Vegetables"
                    required
                  />
                </div>

                <div>
                  <Label htmlFor="ingredients">Ingredients</Label>
                  <Textarea
                    id="ingredients"
                    value={formData.ingredients}
                    onChange={(e) => setFormData({ ...formData, ingredients: e.target.value })}
                    placeholder="List all ingredients with quantities..."
                    rows={3}
                    required
                  />
                </div>

                <div>
                  <Label htmlFor="instructions">Instructions</Label>
                  <Textarea
                    id="instructions"
                    value={formData.instructions}
                    onChange={(e) => setFormData({ ...formData, instructions: e.target.value })}
                    placeholder="Step-by-step cooking instructions..."
                    rows={4}
                    required
                  />
                </div>

                <div className="grid grid-cols-3 gap-4">
                  <div>
                    <Label htmlFor="prepTime">Prep Time (min)</Label>
                    <Input
                      id="prepTime"
                      type="number"
                      value={formData.prepTime}
                      onChange={(e) => setFormData({ ...formData, prepTime: Number(e.target.value) })}
                      min="0"
                    />
                  </div>
                  <div>
                    <Label htmlFor="cookTime">Cook Time (min)</Label>
                    <Input
                      id="cookTime"
                      type="number"
                      value={formData.cookTime}
                      onChange={(e) => setFormData({ ...formData, cookTime: Number(e.target.value) })}
                      min="0"
                    />
                  </div>
                  <div>
                    <Label htmlFor="servings">Servings</Label>
                    <Input
                      id="servings"
                      type="number"
                      value={formData.servings}
                      onChange={(e) => setFormData({ ...formData, servings: Number(e.target.value) })}
                      min="1"
                      required
                    />
                  </div>
                </div>

                <div className="grid grid-cols-4 gap-4">
                  <div>
                    <Label htmlFor="calories">Calories</Label>
                    <Input
                      id="calories"
                      type="number"
                      value={formData.calories}
                      onChange={(e) => setFormData({ ...formData, calories: Number(e.target.value) })}
                      min="0"
                    />
                  </div>
                  <div>
                    <Label htmlFor="protein">Protein (g)</Label>
                    <Input
                      id="protein"
                      type="number"
                      step="0.1"
                      value={formData.protein}
                      onChange={(e) => setFormData({ ...formData, protein: Number(e.target.value) })}
                      min="0"
                    />
                  </div>
                  <div>
                    <Label htmlFor="carbs">Carbs (g)</Label>
                    <Input
                      id="carbs"
                      type="number"
                      step="0.1"
                      value={formData.carbs}
                      onChange={(e) => setFormData({ ...formData, carbs: Number(e.target.value) })}
                      min="0"
                    />
                  </div>
                  <div>
                    <Label htmlFor="fat">Fat (g)</Label>
                    <Input
                      id="fat"
                      type="number"
                      step="0.1"
                      value={formData.fat}
                      onChange={(e) => setFormData({ ...formData, fat: Number(e.target.value) })}
                      min="0"
                    />
                  </div>
                </div>

                <div>
                  <Label htmlFor="notes">Notes (optional)</Label>
                  <Textarea
                    id="notes"
                    value={formData.notes}
                    onChange={(e) => setFormData({ ...formData, notes: e.target.value })}
                    placeholder="Any additional notes..."
                    rows={2}
                  />
                </div>

                <div className="flex gap-2 pt-4">
                  <Button type="submit" disabled={isLoading} className="flex-1">
                    {isLoading ? 'Saving...' : editingPlan ? 'Update Meal' : 'Add Meal'}
                  </Button>
                  <Button type="button" variant="outline" onClick={resetForm}>
                    Cancel
                  </Button>
                </div>
              </form>
            </CardContent>
          </Card>
        </div>
      )}
    </div>
  );
};

export default MealPlanningPage;
