import React, { useState, useRef } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Upload, File, X, Loader2 } from 'lucide-react';
import { useHealthStore } from '@/store/healthStore';
import { HealthDocument } from '@/types/Health';
import { toast } from 'sonner';

const MAX_FILE_SIZE = 10 * 1024 * 1024; // 10MB
const ALLOWED_FILE_TYPES = [
  'application/pdf',
  'image/jpeg',
  'image/png',
  'image/gif',
  'application/msword',
  'application/vnd.openxmlformats-officedocument.wordprocessingml.document',
  'application/vnd.ms-excel',
  'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
  'text/plain'
];

const FILE_CATEGORIES = [
  { value: 'medical-record', label: 'Medical Record' },
  { value: 'lab-result', label: 'Lab Result' },
  { value: 'prescription', label: 'Prescription' },
  { value: 'insurance', label: 'Insurance' },
  { value: 'report', label: 'Report' },
  { value: 'other', label: 'Other' }
];

export const DocumentUpload: React.FC = () => {
  const { currentPersonId, uploadDocument, people } = useHealthStore() as any;
  const currentPerson = people.find((p: any) => p.id === currentPersonId);
  
  const fileInputRef = useRef<HTMLInputElement>(null);
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');
  const [category, setCategory] = useState<string>('other');
  const [tags, setTags] = useState('');
  const [isConfidential, setIsConfidential] = useState(false);
  const [isUploading, setIsUploading] = useState(false);

  const handleFileSelect = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    // Validate file size
    if (file.size > MAX_FILE_SIZE) {
      toast.error(`File size must be less than ${MAX_FILE_SIZE / (1024 * 1024)}MB`);
      return;
    }

    // Validate file type
    if (!ALLOWED_FILE_TYPES.includes(file.type)) {
      toast.error('Invalid file type. Please upload PDF, Word, Excel, images, or text files.');
      return;
    }

    setSelectedFile(file);
    // Auto-fill title if empty
    if (!title) {
      const fileName = file.name.split('.').slice(0, -1).join('.');
      setTitle(fileName);
    }
  };

  const handleRemoveFile = () => {
    setSelectedFile(null);
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  const handleUpload = async () => {
    if (!selectedFile) {
      toast.error('Please select a file to upload');
      return;
    }

    if (!title.trim()) {
      toast.error('Please enter a title for the document');
      return;
    }

    setIsUploading(true);

    try {
      const documentData: Omit<HealthDocument, 'id' | 'uploadDate' | 'driveFileId' | 'driveUrl'> = {
        personId: currentPersonId,
        title: title.trim(),
        fileName: selectedFile.name,
        fileType: selectedFile.type,
        fileSize: selectedFile.size,
        category: category as 'medical-record' | 'lab-result' | 'prescription' | 'insurance' | 'report' | 'other',
        tags: tags.split(',').map(tag => tag.trim()).filter(Boolean),
        isConfidential,
        ...(description.trim() && { description: description.trim() })
      };

      const success = await uploadDocument(documentData, selectedFile);
      
      if (success) {
        toast.success('Document uploaded successfully!');
        // Reset form
        setSelectedFile(null);
        setTitle('');
        setDescription('');
        setCategory('other');
        setTags('');
        setIsConfidential(false);
        if (fileInputRef.current) {
          fileInputRef.current.value = '';
        }
      } else {
        toast.error('Failed to upload document');
      }
      
      setIsUploading(false);
    } catch (error) {
      console.error('Upload error:', error);
      toast.error('Error uploading document');
      setIsUploading(false);
    }
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <Upload className="h-5 w-5" />
          Upload Health Document
        </CardTitle>
      </CardHeader>
      <CardContent>
        <div className="space-y-4">
          {/* File Selection */}
          <div>
            <Label htmlFor="file">Select File</Label>
            <div className="mt-2">
              <input
                ref={fileInputRef}
                id="file"
                type="file"
                onChange={handleFileSelect}
                accept=".pdf,.doc,.docx,.xls,.xlsx,.jpg,.jpeg,.png,.gif,.txt"
                className="hidden"
              />
              
              {!selectedFile ? (
                <Button
                  type="button"
                  variant="outline"
                  onClick={() => fileInputRef.current?.click()}
                  className="w-full"
                >
                  <File className="h-4 w-4 mr-2" />
                  Choose File
                </Button>
              ) : (
                <div className="flex items-center justify-between p-3 border rounded-lg bg-gray-50">
                  <div className="flex items-center gap-2">
                    <File className="h-4 w-4 text-gray-600" />
                    <span className="text-sm font-medium">{selectedFile.name}</span>
                    <span className="text-xs text-gray-500">
                      ({(selectedFile.size / 1024).toFixed(2)} KB)
                    </span>
                  </div>
                  <Button
                    type="button"
                    variant="ghost"
                    size="sm"
                    onClick={handleRemoveFile}
                  >
                    <X className="h-4 w-4" />
                  </Button>
                </div>
              )}
            </div>
          </div>

          {/* Document Details */}
          <div>
            <Label htmlFor="title">Title *</Label>
            <Input
              id="title"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              placeholder="Enter document title"
              className="mt-1"
            />
          </div>

          <div>
            <Label htmlFor="category">Category</Label>
            <Select value={category} onValueChange={setCategory}>
              <SelectTrigger className="mt-1">
                <SelectValue placeholder="Select category" />
              </SelectTrigger>
              <SelectContent>
                {FILE_CATEGORIES.map((cat) => (
                  <SelectItem key={cat.value} value={cat.value}>
                    {cat.label}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

          <div>
            <Label htmlFor="description">Description</Label>
            <Textarea
              id="description"
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              placeholder="Add a description (optional)"
              rows={3}
              className="mt-1"
            />
          </div>

          <div>
            <Label htmlFor="tags">Tags</Label>
            <Input
              id="tags"
              value={tags}
              onChange={(e) => setTags(e.target.value)}
              placeholder="Enter tags separated by commas"
              className="mt-1"
            />
            <p className="text-xs text-gray-500 mt-1">
              e.g., blood test, 2024, cholesterol
            </p>
          </div>

          <div className="flex items-center gap-2">
            <input
              id="confidential"
              type="checkbox"
              checked={isConfidential}
              onChange={(e) => setIsConfidential(e.target.checked)}
              className="rounded border-gray-300"
            />
            <Label htmlFor="confidential" className="font-normal cursor-pointer">
              Mark as confidential
            </Label>
          </div>

          {/* Upload Button */}
          <Button
            onClick={handleUpload}
            disabled={!selectedFile || !title.trim() || isUploading}
            className="w-full"
          >
            {isUploading ? (
              <>
                <Loader2 className="h-4 w-4 mr-2 animate-spin" />
                Uploading...
              </>
            ) : (
              <>
                <Upload className="h-4 w-4 mr-2" />
                Upload Document
              </>
            )}
          </Button>

          {currentPerson && currentPerson.relationship !== 'self' && (
            <p className="text-xs text-gray-500 text-center">
              This document will be uploaded to {currentPerson.name}'s folder
            </p>
          )}
        </div>
      </CardContent>
    </Card>
  );
};
