export const useDashboardCustomization = create<DashboardCustomizationState>()(
  persist(
    (set, get) => ({
      cards: defaultCards,

      toggleCardVisibility: (cardId) =>
        set((state) => ({
          cards: state.cards.map((card) =>
            card.id === cardId ? { ...card, isVisible: !card.isVisible } : card,
          ),
        })),

      resetToDefault: () => set({ cards: [...defaultCards] }),
    }),
    {
      name: "dashboard-customization",
      version: 1,
    },
  ),
);
