import React, { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Switch } from '@/components/ui/switch';
import { Badge } from '@/components/ui/badge';
import { ScrollArea } from '@/components/ui/scroll-area';
import { toast } from 'sonner';
import {
  Settings,
  Eye,
  EyeOff,
  RotateCcw,
  Layout,
} from 'lucide-react';
import { useDashboardCustomization } from '@/store/dashboardCustomizationStore';
import { cn } from '@/lib/utils';

export const DashboardCustomizationToolbar: React.FC = () => {
  const {
    cards,
    toggleCardVisibility,
    resetToDefault,
  } = useDashboardCustomization();

  const [isOpen, setIsOpen] = useState(false);

  const handleResetToDefault = () => {
    resetToDefault();
    toast.success('Reset to default layout');
  };

  const visibleCards = cards.filter(card => card.isVisible);
  const hiddenCards = cards.filter(card => !card.isVisible);

  const cardsByCategory = cards.reduce((acc, card) => {
    if (!acc[card.category]) {
      acc[card.category] = [];
    }
    acc[card.category].push(card);
    return acc;
  }, {} as Record<string, typeof cards>);

  return (
    <Card className="mb-4 border-primary/20 bg-gradient-to-r from-primary/5 to-secondary/5">
      <CardHeader className="pb-3">
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-3">
            <Settings className="h-5 w-5 text-primary" />
            <CardTitle className="text-lg">Dashboard Cards</CardTitle>
            <Badge variant="secondary" className="ml-2">
              {visibleCards.length} of {cards.length} visible
            </Badge>
          </div>
          
          <div className="flex items-center gap-2">
            <Button
              variant="outline"
              size="sm"
              onClick={handleResetToDefault}
            >
              <RotateCcw className="h-4 w-4 mr-2" />
              Reset
            </Button>
            
            <Button
              variant={isOpen ? "default" : "outline"}
              size="sm"
              onClick={() => setIsOpen(!isOpen)}
            >
              <Layout className="h-4 w-4 mr-2" />
              {isOpen ? "Hide" : "Manage Cards"}
            </Button>
          </div>
        </div>
      </CardHeader>

      {isOpen && (
        <CardContent className="pt-0">
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
            {/* Quick Actions */}
            <div className="space-y-3">
              <h4 className="text-sm font-medium flex items-center gap-2">
                <Layout className="h-4 w-4" />
                Quick Actions
              </h4>
              
              <div className="space-y-2">
                <Button
                  variant="outline"
                  size="sm"
                  className="w-full justify-start"
                  onClick={() => {
                    cards.forEach(card => {
                      if (!card.isVisible) toggleCardVisibility(card.id);
                    });
                    toast.success('All cards shown');
                  }}
                >
                  <Eye className="h-4 w-4 mr-2" />
                  Show All Cards
                </Button>
                
                <Button
                  variant="outline"
                  size="sm"
                  className="w-full justify-start"
                  onClick={() => {
                    cards.forEach(card => {
                      if (card.isVisible && card.category !== 'stat') toggleCardVisibility(card.id);
                    });
                    toast.success('Only stats cards shown');
                  }}
                >
                  <EyeOff className="h-4 w-4 mr-2" />
                  Show Only Stats
                </Button>
              </div>
            </div>

            {/* Card Visibility by Category */}
            <div className="space-y-3">
              <h4 className="text-sm font-medium flex items-center gap-2">
                <Eye className="h-4 w-4" />
                Categories
              </h4>
              
              <ScrollArea className="h-48">
                <div className="space-y-3">
                  {Object.entries(cardsByCategory).map(([category, categoryCards]) => (
                    <div key={category} className="space-y-2">
                      <div className="flex items-center justify-between">
                        <Badge variant="outline" className="text-xs capitalize">
                          {category} ({categoryCards.length})
                        </Badge>
                        <div className="flex gap-1">
                          <Button
                            size="sm"
                            variant="ghost"
                            className="h-6 w-6 p-0"
                            onClick={() => {
                              categoryCards.forEach(card => {
                                if (!card.isVisible) toggleCardVisibility(card.id);
                              });
                              toast.success(`Showing all ${category} cards`);
                            }}
                          >
                            <Eye className="h-3 w-3" />
                          </Button>
                          <Button
                            size="sm"
                            variant="ghost"
                            className="h-6 w-6 p-0"
                            onClick={() => {
                              categoryCards.forEach(card => {
                                if (card.isVisible) toggleCardVisibility(card.id);
                              });
                              toast.success(`Hiding all ${category} cards`);
                            }}
                          >
                            <EyeOff className="h-3 w-3" />
                          </Button>
                        </div>
                      </div>
                      
                      <div className="space-y-1">
                        {categoryCards.map((card) => (
                          <div
                            key={card.id}
                            className="flex items-center justify-between text-xs p-2 rounded border"
                          >
                            <span className={cn(
                              "truncate",
                              !card.isVisible && "text-muted-foreground line-through"
                            )}>
                              {card.name}
                            </span>
                            <Switch
                              checked={card.isVisible}
                              onCheckedChange={() => toggleCardVisibility(card.id)}
                              className="ml-2"
                            />
                          </div>
                        ))}
                      </div>
                    </div>
                  ))}
                </div>
              </ScrollArea>
            </div>

            {/* Dashboard Stats */}
            <div className="space-y-3">
              <h4 className="text-sm font-medium flex items-center gap-2">
                <Badge className="h-4 w-4" />
                Dashboard Stats
              </h4>
              
              <div className="space-y-3">
                <div className="grid grid-cols-2 gap-2 text-sm">
                  <div className="flex justify-between">
                    <span>Total:</span>
                    <Badge variant="secondary">{cards.length}</Badge>
                  </div>
                  
                  <div className="flex justify-between">
                    <span>Visible:</span>
                    <Badge variant="default">{visibleCards.length}</Badge>
                  </div>
                  
                  <div className="flex justify-between">
                    <span>Hidden:</span>
                    <Badge variant="outline">{hiddenCards.length}</Badge>
                  </div>
                </div>
                
                <div className="text-xs space-y-1">
                  {Object.entries(cardsByCategory).map(([category, categoryCards]) => (
                    <div key={category} className="flex justify-between">
                      <span className="capitalize">{category}:</span>
                      <span>{categoryCards.filter(c => c.isVisible).length}/{categoryCards.length}</span>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>

          {/* Instructions */}
          <div className="mt-4 p-3 bg-muted/30 rounded-md">
            <p className="text-xs text-muted-foreground">
              <strong>Instructions:</strong> Use the switches to show/hide individual cards. 
              Use category buttons to show/hide entire categories. 
              Hidden cards will be automatically removed from the dashboard layout.
            </p>
          </div>
        </CardContent>
      )}
    </Card>
  );
}; 