import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Button } from '@/components/ui/button';
import { FolderOpen, Save, Settings } from 'lucide-react';
import { useHealthStore } from '@/store/healthStore';
import { toast } from 'sonner';

export const FolderConfiguration: React.FC = () => {
  const { 
    currentPersonId, 
    getPersonConfig, 
    saveHealthConfig, 
    updateHealthConfig,
    people 
  } = useHealthStore();
  
  const currentPerson = people.find(p => p.id === currentPersonId);
  const existingConfig = getPersonConfig();
  
  const [folderId, setFolderId] = useState(existingConfig?.driveSettings.folderId || '');
  const [isEditing, setIsEditing] = useState(false);
  const [isSaving, setIsSaving] = useState(false);

  useEffect(() => {
    const config = getPersonConfig();
    setFolderId(config?.driveSettings.folderId || '');
  }, [currentPersonId, getPersonConfig]);

  const handleSave = async () => {
    if (!folderId.trim()) {
      toast.error('Please enter a valid folder ID');
      return;
    }

    setIsSaving(true);
    
    try {
      let success = false;
      
      if (existingConfig) {
        // Update existing config
        success = await updateHealthConfig(existingConfig.id, {
          driveSettings: {
            ...existingConfig.driveSettings,
            folderId: folderId.trim()
          }
        });
      } else {
        // Create new config
        success = await saveHealthConfig({
          personId: currentPersonId,
          driveSettings: {
            folderId: folderId.trim(),
            autoUpload: false,
            maxFileSize: 10,
            allowedFileTypes: ['application/pdf', 'image/jpeg', 'image/png', 'image/gif', 'application/msword', 'application/vnd.openxmlformats-officedocument.wordprocessingml.document']
          },
          spreadsheetSettings: {
            spreadsheetId: '',
            autoSync: true,
            syncInterval: 15
          },
          notificationSettings: {
            emailReminders: false,
            reminderFrequency: 'weekly',
            healthGoalAlerts: true
          }
        });
      }
      
      if (success) {
        toast.success('Folder configuration saved successfully!');
        setIsEditing(false);
      } else {
        toast.error('Failed to save folder configuration');
      }
    } catch (error) {
      toast.error('Error saving configuration');
      console.error('Error saving folder configuration:', error);
    } finally {
      setIsSaving(false);
    }
  };

  const handleCancel = () => {
    setFolderId(existingConfig?.driveSettings.folderId || '');
    setIsEditing(false);
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <Settings className="h-5 w-5" />
          Document Storage Configuration
        </CardTitle>
      </CardHeader>
      <CardContent>
        <div className="space-y-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-2">
              <FolderOpen className="h-4 w-4 text-gray-500" />
              <span className="text-sm font-medium">
                {currentPerson?.name || 'Self'}'s Documents Folder
              </span>
            </div>
            {!isEditing && (
              <Button
                variant="outline"
                size="sm"
                onClick={() => setIsEditing(true)}
              >
                Configure
              </Button>
            )}
          </div>
          
          {isEditing && (
            <div className="space-y-4 pt-2">
              <div>
                <Label htmlFor="folderId">Google Drive Folder ID</Label>
                <Input
                  id="folderId"
                  value={folderId}
                  onChange={(e) => setFolderId(e.target.value)}
                  placeholder="Enter the Google Drive folder ID"
                  className="mt-1"
                />
                <p className="text-xs text-gray-500 mt-2">
                  This folder will be used to store all health documents for {currentPerson?.name || 'you'}.
                </p>
              </div>
              
              <div className="flex gap-2">
                <Button
                  onClick={handleSave}
                  disabled={isSaving}
                  size="sm"
                  className="flex items-center gap-2"
                >
                  <Save className="h-4 w-4" />
                  {isSaving ? 'Saving...' : 'Save'}
                </Button>
                <Button
                  variant="outline"
                  onClick={handleCancel}
                  disabled={isSaving}
                  size="sm"
                >
                  Cancel
                </Button>
              </div>
            </div>
          )}
          
          {!isEditing && folderId && (
            <div className="text-sm text-gray-600">
              <span className="font-medium">Folder ID:</span> 
              <code className="ml-2 px-2 py-1 bg-gray-100 rounded text-xs">{folderId}</code>
            </div>
          )}
          
          {!isEditing && !folderId && (
            <div className="text-sm text-gray-500 italic">
              No folder configured. Documents will be stored in the default health folder.
            </div>
          )}
        </div>
      </CardContent>
    </Card>
  );
}; 