import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { CheckSquare, Eye, EyeOff, ArrowRight } from 'lucide-react';
import { dataService } from '@/services/DataService';
import { TodoEntry } from '@/types/DataModels';
import { useNavigation } from '@/hooks/useConsolidatedApp';
import { QuickAddTask } from '@/components/ui/QuickAddTask';
import { InlineEditableTask } from '@/components/ui/InlineEditableTask';
import { formatTodoForDisplay } from '@/utils/todoHelpers';

export const TodoCard: React.FC = () => {
  const [todos, setTodos] = useState<TodoEntry[]>([]);
  const [showCompleted, setShowCompleted] = useState(false);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const { setCurrentView } = useNavigation();

  useEffect(() => {
    loadTodos();
  }, []);

  const loadTodos = async () => {
    try {
      setLoading(true);
      setError(null);
      const result = await dataService.todos.getAll();
      if (result.success && result.data) {
        // Sort todos consistently: active first, then by creation date (newest first)
        const sortedTodos = result.data.data.sort((a, b) => {
          // Completed tasks at the bottom
          if (a.isCompleted !== b.isCompleted) {
            return a.isCompleted ? 1 : -1;
          }
          // Sort by creation date (newest first)
          return new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime();
        });
        
        setTodos(sortedTodos);
      } else {
        setError(result.error || 'Failed to load tasks');
      }
    } catch (err) {
      console.error('Failed to load todos:', err);
      setError('Failed to load tasks');
    } finally {
      setLoading(false);
    }
  };

  const handleTaskAdded = (newTask: TodoEntry) => {
    setTodos(prev => [newTask, ...prev]);
    setError(null);
  };

  const handleTaskUpdated = (updatedTask: TodoEntry) => {
    setTodos(prev => prev.map(task => 
      task.id === updatedTask.id ? updatedTask : task
    ));
  };

  const handleTaskDeleted = (taskId: string) => {
    setTodos(prev => prev.filter(task => task.id !== taskId));
  };

  const activeTodos = todos.filter(todo => !todo.isCompleted);
  const completedTodos = todos.filter(todo => todo.isCompleted);
  const displayTodos = showCompleted ? todos : activeTodos;

  if (loading) {
    return (
      <Card className="min-h-[300px]">
        <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
          <CardTitle className="text-sm font-medium flex items-center gap-2">
            <CheckSquare className="h-4 w-4" />
            Tasks
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="flex items-center justify-center h-32">
            <div className="text-center">
              <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-primary mx-auto mb-2"></div>
              <p className="text-sm text-muted-foreground">Loading...</p>
            </div>
          </div>
        </CardContent>
      </Card>
    );
  }

  if (error) {
    return (
      <Card className="min-h-[300px] border-red-200">
        <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
          <CardTitle className="text-sm font-medium flex items-center gap-2">
            <CheckSquare className="h-4 w-4" />
            Tasks
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="text-center py-6">
            <p className="text-sm text-red-600 mb-3">{error}</p>
            <Button size="sm" onClick={loadTodos} variant="outline">
              Try Again
            </Button>
          </div>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card className="min-h-[300px]">
      <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
        <CardTitle className="text-sm font-medium flex items-center gap-2">
          <CheckSquare className="h-4 w-4" />
          Tasks
          {activeTodos.length > 0 && (
            <span className="text-xs bg-primary text-primary-foreground px-1.5 py-0.5 rounded-full">
              {activeTodos.length}
            </span>
          )}
        </CardTitle>
        
        <div className="flex items-center gap-1">
          {completedTodos.length > 0 && (
            <Button
              variant="ghost"
              size="sm"
              onClick={() => setShowCompleted(!showCompleted)}
              className="h-8 w-8 p-0"
              title={showCompleted ? "Hide completed" : "Show completed"}
            >
              {showCompleted ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
            </Button>
          )}
          
          <Button
            variant="ghost"
            size="sm"
            onClick={() => setCurrentView('todos')}
            className="h-8 w-8 p-0"
            title="View all tasks"
          >
            <ArrowRight className="h-4 w-4" />
          </Button>
        </div>
      </CardHeader>
      
      <CardContent className="space-y-3">
        {/* Quick Add Task */}
        <QuickAddTask 
          onTaskAdded={handleTaskAdded}
          compact
        />
        
        {/* Tasks List */}
        {displayTodos.length === 0 ? (
          <div className="text-center py-6">
            <CheckSquare className="h-8 w-8 mx-auto text-muted-foreground mb-2" />
            <p className="text-sm text-muted-foreground">
              {showCompleted ? 'No tasks found' : activeTodos.length === 0 ? 'No tasks yet' : 'No active tasks'}
            </p>
            {!showCompleted && activeTodos.length === 0 && (
              <p className="text-xs text-muted-foreground mt-1">
                Add your first task above
              </p>
            )}
          </div>
        ) : (
          <div className="space-y-2">
            {displayTodos.map(todo => {
              // Format todo data consistently before passing to component
              const formattedTodo = formatTodoForDisplay(todo);
              return (
                <InlineEditableTask
                  key={formattedTodo.id}
                  task={todo} // Pass original todo object for component compatibility
                  onTaskUpdated={handleTaskUpdated}
                  onTaskDeleted={handleTaskDeleted}
                  compact
                />
              );
            })}
          </div>
        )}
        
        {/* Completed Tasks Info */}
        {!showCompleted && completedTodos.length > 0 && (
          <div className="text-center pt-2 border-t">
            <Button
              variant="ghost"
              size="sm"
              onClick={() => setShowCompleted(true)}
              className="text-xs text-muted-foreground hover:text-foreground"
            >
              {completedTodos.length} completed task{completedTodos.length === 1 ? '' : 's'}
            </Button>
          </div>
        )}
      </CardContent>
    </Card>
  );
}; 